.sidebar {
  border: 1px solid $color-whisper;
  box-sizing: border-box;
  border-radius: 8px;
  min-width: 280px;
  width: 23.59%;
  padding: 8px;
  margin-right: 24px;

  @include vp--1023 {
    width: 100%;
    margin-bottom: 28px;
  }
}

.sidebar--opened {
  display: block;
}

.sidebar--closed {
  @include vp--1023 {
    display: none;
  }
}

.sidebar__toggle {
  display: none;

  width: 100%;
  border: 1px solid $color-whisper;
  border-radius: 8px;
  background-color: $color-transparent;
  text-align: left;
  padding: 12px 16px;
  margin-bottom: 16px;

  & svg {
    color: $color-dark-gray;
  }

  @include vp--1023 {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.sidebar__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar__item {
  padding-left: 16px;
  padding-top: 12px;
  padding-bottom: 12px;
  border-radius: 4px;
}

.sidebar__item--active {
  background-color: $color-white-smoke;

  & .sidebar__link {
    cursor: default;
  }
}

.sidebar__link {
  color: $color-nero;
  cursor: pointer;
  width: 100%;
  display: block;
}

.sidebar__btn {
  background-color: $color-transparent;
  border: none;
  padding-left: 16px;
  padding-top: 12px;
  padding-bottom: 12px;
  margin-top: 8px;
  width: 100%;

  cursor: pointer;
  color: $color-nero;
  text-align: left;

  position: relative;

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background-color: $color-whisper;
    position: absolute;
    top: 0;
    left: 0;
  }
}

