.gallery-slider {
  position: relative;
  display: flex;
  height: 412px;

  overflow: hidden;

  @include vp--1023 {
    height: 320px;
  }
}

// стили вертикального слайдера
.gallery-slider__col {
  position: relative;
  margin-right: 24px;

  .swiper-thumbs {
    display: flex;
    flex-direction: column;
    width: 84px;
    height: calc(100% - 48px);
  }

  .swiper-slide {
    border: 1px solid $color-safety-orange;
    border-radius: 4px;
    overflow: hidden;
    opacity: 0.5;

    &-thumb-active {
      opacity: 1;
    }
  }

  img {
    height: 100%;
    object-fit: cover;
  }

  @include vp--1023 {
    display: none;
  }
}

.gallery-slider__next,
.gallery-slider__prev {
  position: relative;
  width: 100%;
  height: 24px;
  top: 0;
  left: 0;

  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;

    border-left: 2px solid $color-safety-orange;
    border-top: 2px solid $color-safety-orange;
    border-radius: 2px 0 0 0;

    transform: translate(-50%, -50%) rotate(45deg);
  }

  @include non-touch {
    &:hover {
      opacity: 0.5;
    }
  }
}

.gallery-slider__next::after {
  transform: translate(-50%, -50%) rotate(-135deg);
}

.swiper-button-disabled::after {
  border-color: $color-seashell;
}

// стили слайдера
.gallery-slider__images {
  position: relative;
  height: 100%;

  .swiper-container {
    height: 100%;
  }

  .gallery-slider__badges {
    position: absolute;
    top: 24px;
    left: 24px;

    z-index: 11;

    @include vp--1023 {
      top: 0;
      left: 0;
    }
  }

  .product-badge {
    margin-bottom: 4px;
  }

  img {
    border-radius: 8px;
    margin: 0 auto;
  }

  @include vp--1023 {
    width: calc(100%);
    padding-bottom: 20px;

    img {
      height: 100%;
      object-fit: contain;
    }
  }
}

// стили пагинации
.gallery-slider__pagination {
  display: none;
  position: absolute;
  justify-content: space-between;
  bottom: 10px;
  width: 100%;
  margin-right: -2px;

  @include vp--1023 {
    display: flex;
  }

  .swiper-pagination-bullet {
    position: relative;
    flex-grow: 1;
    margin-right: 2px;
    height: 2px;

    background-color: $color-white-smoke-2;
    border-radius: 2px;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 10px;
      top: 50%;
      left: 0;

      transform: translateY(-50%);
    }
  }

  .swiper-pagination-bullet-active {
    background-color: $color-safety-orange;
  }
}
