.price-box {
  display: flex;

  font-weight: 500;

  &--direction-column {
    flex-direction: column;
  }
}

.price-box__price {
  margin-top: 0;
  margin-bottom: 0;

  &--right-margin {
    margin-right: 8px;
  }
}

.price-box__old-price {
  margin-top: 0;
  margin-bottom: 0;

  font-weight: 400;
  font-size: 14px;
  line-height: 23px;
  color: $color-charcoal;
  text-decoration: line-through;
}
