.link {
  @include non-touch {
    &:hover {
      color: $color-apple;
    }

    &--color:hover {
      color: $color-tacao;
    }
  }

  position: relative;

  display: inline-flex;
  align-items: center;

  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: $color-charcoal;

  cursor: pointer;

  transition: $trans--300;
  transition-property: color;

  &:active {
    color: rgba($color-apple, 0.7);
  }

  &--color:active {
    color: rgba($color-tacao, 0.7);
  }

  &--color {
    color: $color-default-white;
  }

  &--size-big {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  &--icon-left .link__icon {
    margin-right: 19px;
  }

  &--icon-right .link__icon {
    order: 10;
    margin-left: 19px;

    transition: $trans--300;
    transition-property: transform;
  }

  &--icon-double .link__icon--first {
    margin-right: 19px;
  }

  &--icon-double .link__icon--second {
    margin-left: 19px;

    transform: rotate(0deg);

    transition: $trans--300;
    transition-property: transform;
  }

  &--menu-close .link__icon--second {
    transform: rotate(180deg);
  }

  &--menu-close .button {
    margin-right: 25px;
  }
}

.link__icon {
  flex-shrink: 0;
  transition: $trans--300;
  transition-property: transform;
}

.link--menu-close .link__icon--twist {
  transform: rotate(-180deg);
}

.link--menu-open .link__icon--twist {
  transform: rotate(0deg);
}

.link .button {
  position: static;
}
