.input-field {
  @include non-touch {
    input:hover {
      outline: none;
      box-shadow: inset 0 0 0 1px $color-tacao;
    }

    input:hover + svg {
      color: $color-tacao;
    }
  }

  position: relative;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  font-size: 14px;
  line-height: 24px;
  color: $color-dark-gray;

  input {
    @include vp--767 {
      padding-top: 8px;
      padding-bottom: 8px;

      font-size: 14px;
    }

    flex-grow: 1;
    margin: 0;
    padding: 12px 16px;

    font-size: 16px;
    line-height: 24px;
    color: $color-nero;

    border: none;
    border-radius: 4px;

    transition: $trans--300;
    transition-property: box-shadow;
  }

  input::placeholder {
    color: $color-very-light-grey;
  }

  input:focus {
    outline: none;
    box-shadow: inset 0 0 0 1px $color-tacao;
  }

  input:focus + svg {
    color: $color-tacao;
  }

  input:read-only {
    color: $color-dark-gray;

    background-color: $color-white-smoke;
  }

  span {
    color: $color-red-orange;
  }

  &--theme-border input {
    background-color: $color-default-white;
    box-shadow: inset 0 0 0 1px $color-whisper;
  }

  &--icon input {
    padding-right: 50px;
  }

  &--icon svg {
    position: absolute;
    top: 50%;
    right: 18px;

    transform: translateY(-50%);

    transition: $trans--300;
    transition-property: color;
  }

  &--theme-range-field {
    input {
      width: 100%;
      padding: 8px 16px;

      font-size: 14px;
    }
  }
}
