.home-page__tabs-controls {
  display: inline-block;
  margin-bottom: 10px;
  border-bottom: 2px solid $color-whisper;

  @include vp--1023 {
    margin-bottom: 16px;
  }
}

.swiper-container.home-page__tabs-controls {
  overflow: visible;
  min-width: 0;
  display: flex;
  flex-direction: row;
}

.home-page__tabs-controls-wrapper {
  @media (min-width: $vp--768) {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}

.swiper-wrapper.home-page__tabs-controls-wrapper {
  min-width: 0;
}

.swiper-slide.home-page__tabs-controls-item {
  width: auto;
}

.home-page__tabs-content {
  display: none;

  &--active {
    width: 100%;
    margin-bottom: 24px;
    display: block;

    @media (min-width: 769px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 20px;
      grid-row-gap: 20px;
      justify-content: space-between;
    }

    @include vp--1023 {
      grid-template-columns: 1fr;
    }
  }
}

.home-page__tabs-item {
  max-width: 628px;

  @include vp--1023 {
    max-width: 100%;
    overflow: visible;
    transition-duration: 0ms;
    transform: translate3d(0, 0, 0);
  }

  &--vp-hidden {
    @include vp--1023 {
      display: none;
    }
  }
}

.home-page__banner {
  margin-bottom: 24px;
}

.home-page__categories {
  margin-bottom: 24px;
  transition-duration: 0ms;
  transform: translate3d(0, 0, 0);
  overflow: visible;

  @include vp--767 {
    margin-bottom: 4px;
  }
}

