.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 302px;
  width: 100%;
  padding: 16px;
}

.product-card__badges {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 4px;
}

.product-card__slider {
  overflow: hidden;
  margin-bottom: 8px;
}

.product-card__slider-wrapper {
  display: flex;
}

.product-card__slider-pagination {
  display: flex;
  padding: 11px 0;

  @include vp--1023 {
    display: none;
  }
}

.product-card__slider-pagination-bullet {
  display: block;
  width: 100%;
  height: 2px;
  margin-right: 2px;
  border-radius: 2px;
  background-color: $color-white-smoke;

  &--active {
    background-color: $color-safety-orange;
  }

  &:last-child {
    margin-right: 0;
  }
}

.product-card__meta {
  display: flex;
  margin-bottom: 8px;
}

.product-card__meta-item {
  display: flex;
  align-items: center;
  margin-right: 16px;
  font-size: 12px;
  line-height: 16px;
  color: $color-charcoal;

  &:last-child {
    margin-right: 0;
  }
}

.product-card__rate-icon {
  margin-right: 4px;
  color: $color-safety-orange;
}

.product-card__comment--empty,
.product-card__comment--empty .product-card__comment-icon {
  color: $color-very-light-grey;
}

.product-card__comment-icon {
  margin-right: 4px;
  color: $color-safety-orange;
}

.product-card__stock {
  margin-left: auto;

  &--not-available {
    color: $color-red-orange;
  }

  &--order {
    color: $color-safety-orange;
  }
}

.product-card__stock--order .product-card__stock-icon {
  margin-right: 0;
  margin-left: 4px;
  color: $color-charcoal;
}

.product-card__stock-icon {
  margin-right: 4px;
  color: $color-apple;
}

.product-card__title {
  margin: 0 0 8px;
  min-height: 72px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

.product-card__prices {
  margin: auto 0 8px;
  display: grid;
  column-gap: 8px;
  grid-template-columns: max-content max-content;
  grid-template-rows: auto 24px;
}

.product-card__price {
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
}

.product-card__price-old {
  font-size: 14px;
  line-height: 23px;
  text-decoration: line-through;
  color: $color-charcoal;
}

.product-card__price-badge {
  grid-column: 1 / -1;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 13px;
  line-height: 16px;
  color: $color-default-white;
  background-color: $color-safety-orange;
}

.product-card__buttons {
  display: flex;
}

.product-card__actions {
  display: flex;
  align-items: center;
}

.product-card__action {
  width: 40px;
  height: 40px;
  margin-right: 8px;
  padding: 8px;
  border: none;
  border-radius: 4px;
  color: $color-very-light-grey;
  background-color: transparent;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $color-default-white;
    background-color: $color-safety-orange;
  }

  &:last-child {
    margin-right: 0;
  }
}

.product-card__button-add {
  margin-left: auto;
}
