.action-bar {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  overflow: hidden;

  @include vp--767 {
    display: none;
  }
}

.action-bar__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.action-bar__item {
  flex-grow: 1;
  max-width: 241px;
  padding-right: 1px;
  background-color: $color-sun;

  &:last-child {
    flex-grow: 0;
    padding-right: 0;
  }
}

.action-bar__item:last-child svg {
  margin-right: 0;
}

.action-bar__link {
  @include non-touch {
    &:hover {
      background-color: transparent;
    }
  }

  display: flex;
  align-items: center;
  padding: 16px 24px;
  color: $color-default-white;
  background-color: $color-safety-orange;
  transition: background-color $trans--300;

  &--active {
    background-color: transparent;
  }
}

.action-bar__link--arrow svg {
  transform: rotate(-90deg);
}

.action-bar__link svg {
  order: -1;
  margin-right: 12px;
}

.action-bar__link-count {
  margin-left: 4px;
}
