.product-page {
  padding-top: 22px;
}

.product-page__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 36px;

  .product-page__title {
    margin-top: 0;
    margin-bottom: 5px;

    font-weight: 500;
    font-size: 32px;
    line-height: 48px;
  }
}

.product-page__user-bar {
  display: flex;
  align-items: center;
}

.product-page__user-list {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.product-page__user-item {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  margin-right: 33px;

  background-color: transparent;

  font-size: 14px;
  line-height: 16px;

  .rating {
    margin-right: 37px;
  }

  button {
    padding: 0;
    font-size: 14px;
    line-height: 16px;
  }

  svg {
    margin-right: 10px;
  }

  @include non-touch {
    button:hover,
    button:focus {
      opacity: 0.5;
      background-color: transparent;
    }
  }
}

.product-page__id {
  font-size: 14px;
  line-height: 16px;
  color: $color-dark-gray;

  span {
    margin-left: 8px;
  }
}

.product-page__brand-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 122px;

  span {
    font-size: 14px;
    line-height: 16px;
    color: $color-dark-gray;
  }

  @include vp--1023 {
    position: absolute;
    top: 10px;
    right: 10px;
  }

  @include non-touch {
    &:hover {
      opacity: 0.5;
    }
  }
}

.product-page__content {
  display: grid;
  grid-template-columns: 1fr 302px;
  grid-column-gap: 24px;

  @include vp--1023 {
    display: block;
  }
}

.product-page__tabs {
  width: 100%;

  button {
    flex-grow: 1;
  }
}

.product-page__controls {
  margin-bottom: 24px;
}

.product-page__tabs-buttons {
  display: flex;
}

.product-page__content-tabs {
  margin-bottom: 35px;
}

.product-page__item {
  width: 100%;

  .product-page__inner-wrap {
    padding: 0 15px 0 17px;
  }

  .product-table {
    margin-top: -4px;
  }

  .product-table tbody {
    column-count: 2;
    display: block;
  }

  .product-table__row {
    display: table-row;
  }

  .product-table__row:first-of-type {
    display: none;
  }

  .product-table__cell {
    font-size: 16px;
    line-height: 24px;

    &:first-of-type {
      width: 68%;
    }
  }

  .product-page__link {
    margin-right: 24px;

    font-weight: 500;
    font-size: 18px;
    line-height: 28px;

    &--active {
      color: $color-apple;
      border-color: $color-apple;
    }
  }

  .product-page__delivery-text {
    display: none;
    margin: 18px 0;

    p {
      margin: 0;
    }

    &--active {
      display: block;
    }
  }

  p {
    margin-top: 18px;
    margin-bottom: 24px;
  }

  button {
    flex-grow: 0;
  }
}
