.product-badge {
  display: grid;
  grid-template-columns: 24px auto;
  min-width: 40px;
  min-height: 40px;
  padding: 8px;
  border-radius: 40px;
  background-color: $color-default-white;
  box-shadow: 0 4px 12px rgba($color-default-black, 0.05);

  &--delivery {
    color: $color-safety-orange;
  }

  &--like {
    color: $color-apple;
  }

  &--new {
    color: $color-studio;
  }
}

.product-badge--hover .product-badge__text {
  display: block;
}

.product-badge__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
}

.product-badge__text {
  display: none;
  margin-left: 8px;
  font-size: 14px;
  line-height: 24px;
}
