.map {
  position: relative;
  width: 100%;
  height: 340px;

  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .ymaps-2-1-79-map-copyrights-promo {
    display: none;
  }
  /* stylelint-disable declaration-no-important */
  .ymaps-2-1-79-zoom {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    padding: 0;
    width: 80px;
    height: 36px !important;
    box-shadow: 0 1px 2px 1px #00000026, 0 2px 5px -3px #00000026;
    border-radius: 36px;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 26px;
      left: 50%;
      top: 50%;
      background-color: $color-white-smoke-3;

      transform: translate(-50%, -50%);
      z-index: 1;
    }
  }

  .ymaps-2-1-79-zoom__button {
    position: relative !important;
    height: 100%;
    width: 50%;
    padding: 0;
  }

  .ymaps-2-1-79-zoom__minus,
  .ymaps-2-1-79-zoom__plus {
    display: flex;
    justify-content: center;
    align-items: center;

    .ymaps-2-1-79-zoom__icon {
      background-image: none;
    }
  }

  .ymaps-2-1-79-float-button {
    box-shadow: none;
    border-radius: 0;
  }

  .ymaps-2-1-79-float-button-icon {
    position: relative;

    &::after,
    &::before {
      content: "";
      position: absolute;
      width: 12px;
      height: 2px;
      top: 50%;
      left: 50%;
      background-color: $color-charcoal;

      transform: translate(-50%, -50%);
    }
  }

  .ymaps-2-1-79-zoom__plus .ymaps-2-1-79-float-button-icon::before {
    transform: translate(-50%, -50%) rotate(90deg);
  }

  .ymaps-2-1-79-listbox {
    position: relative;
    padding: 0;
    width: 106px;
    height: 36px !important;
    box-shadow: 0 1px 2px 1px #00000026, 0 2px 5px -3px #00000026;
    border-radius: 36px;
  }

  .ymaps-2-1-79-listbox__button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    border-radius: 36px;
  }

  .ymaps-2-1-79-listbox__button-arrow {
    top: 50%;

    transform: translateY(-50%);
  }

  .ymaps-2-1-79-listbox_opened_yes .ymaps-2-1-79-listbox__button-arrow {
    transform: translateY(-50%) rotate(180deg);
  }

  .ymaps-2-1-79-listbox_opened_no .ymaps-2-1-79-listbox__panel {
    bottom: -9999px;
  }

  .ymaps-2-1-79-listbox__panel {
    position: absolute;
    bottom: 38px;
    border-radius: 8px;

    box-shadow: 0 1px 2px 1px #00000026, 0 2px 5px -3px #00000026 !important;
  }

  .ymaps-2-1-79-listbox__list {
    min-height: 125px !important;
    overflow-y: auto;
  }
  /* stylelint-enable declaration-no-important */
}
