@include non-touch {
  .product-catalog__link-logo:hover {
    opacity: 0.5;
  }
}

.product-catalog {
  @include vp--1023 {
    display: none;
  }

  max-width: 1360px;
  margin: 0 auto;
}

.product-catalog__content {
  position: relative;

  display: flex;
  background-color: $color-default-white;
  box-shadow: 0 0 10px 0 rgba($color-very-light-grey, 0.35);
}

.product-catalog__tabs {
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  width: 243px;
  overflow: hidden;

  background-color: $color-default-white;
  border-right: 1px solid $color-whisper;
  border-bottom-left-radius: 8px;
}

.product-catalog__tabs-content {
  height: 496px;
  overflow-y: scroll;
  flex-grow: 1;
  display: flex;

  background-color: $color-default-white;
  border-bottom-right-radius: 8px;
}

.product-catalog__tab-item {
  display: flex;
  flex-grow: 1;
}

.product-catalog .link {
  color: $color-nero;
}

.product-catalog .product-catalog__tabs .link {
  padding: 16px;

  transition: $trans--300;
  transition-property: color, background-color;
}

.product-catalog .product-catalog__tab--active {
  color: $color-apple;

  background-color: $color-panache;
}

.product-catalog__content-col {
  column-count: 3;
  column-gap: 24px;
  flex-grow: 1;
  margin-top: 0;
  margin-bottom: 0;
  padding: 16px 23px;

  list-style: none;
}

.product-catalog__content-col-item {
  break-inside: avoid;
  margin-bottom: 24px;
}

.product-catalog__item-list {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;
}

.product-catalog__item-list .product-catalog__inner-item + .product-catalog__inner-item {
  margin-top: 8px;
}

.product-catalog__logo-list {
  justify-content: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-right: 40px;
  padding-left: 0;

  list-style: none;
}

.product-catalog__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 145px;
  height: 72px;
}

.product-catalog__logo + .product-catalog__logo {
  margin-top: 16px;
}

.product-catalog__link-logo {
  opacity: 1;
  transition: $trans--300;
  transition-property: opacity;
}
