.sort {
  display: flex;
}

.sort__description {
  margin: 0;
  color: $color-dark-gray;
}

.sort__items {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  list-style: none;
}

.sort__item {
  margin-left: 23.5px;
}

.sort--modal {
  display: block;
  min-width: 288px;

  & .sort__description {
    display: none;
  }

  & .sort__items {
    flex-direction: column;
  }

  & .sort__item {
    width: 100%;
    margin-left: 0;
    padding-left: 16px;

    border-top: 1px solid $color-whisper;
  }
}
