.account {
  margin-bottom: 120px;

  & .account__sidebar-title {
    font-size: 24px;
    line-height: 32px;

    margin-bottom: 16px;
  }

  & .account__sidebar-page {
    margin: 0;
  }

  @include vp--767 {
    margin-bottom: 32px;
  }
}

.account__breadcrumbs,
.account__title,
.account__sidebar {
  @include vp--1023 {
    display: none;
  }
}

.account__title-mobile {
  display: none;

  @include vp--1023 {
    display: flex;
    align-items: center;
    margin: 16px 0;

    & h2 {
      font-size: 18px;
      line-height: 24px;
      font-weight: 500;
      margin: 0;
    }

    & svg {
      display: inline-block;
      color: $color-dark-gray;
      transform: rotate(180deg);
      margin-right: 12px;
    }
  }
}

.account__title-mobile--margin {
  @include vp--767 {
    margin-left: 20px;
  }
}

.account__main {
  display: flex;
  align-items: flex-start;

  @include vp--1023 {
    flex-direction: column;
  }
}

.account__form {
  border: 1px solid $color-whisper;
  border-radius: 8px;
  padding: 24px;
  padding-right: 0;
  max-width: 954px;

  @include vp--767 {
    width: 100%;
    border: none;
    padding: 0;
  }
}

.account__field {
  display: flex;
  flex-wrap: wrap;
  border: none;
  padding: 0;
  margin: 0;

  &:first-child {
    margin-bottom: 48px;
  }

  @include vp--767 {
    flex-direction: column;

    &:first-child {
      margin-bottom: 16px;
    }
  }
}

.account__input {
  // width: 31.56%;
  flex-grow: 1;
  margin-right: 24px;
  margin-bottom: 24px;

  @include vp--767 {
    margin-right: 0;
    margin-bottom: 16px;
  }
}

.account__input:nth-child(5),
.account__input:nth-child(4) {
  @include vp--767 {
    order: 1;
  }
}

.account__radio-wrapper {
  flex-grow: 1;
  margin-right: 24px;

  @include vp--767 {
    margin-right: 0;
    margin-bottom: 16px;
  }
}

.account__sex {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  color: $color-dark-gray;
}

.account__radio-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.account__radio label {
  font-weight: 400;
}

.account__password-title {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}

.account__button {
  min-width: 286px;
  text-align: center;
  display: block;
  background: $color-apple;
  border-radius: 4px;
  padding: 12px 16px;
  border: none;

  font-weight: 500;
  line-height: 24px;
  color: $color-default-white;

  cursor: pointer;

  @include non-touch {
    &:hover {
      background-color: $color-olivine;
    }

    &:disabled:hover {
      background-color: $color-whisper;
    }
  }

  @include vp--767 {
    width: 100%;
    padding: 8px;
  }
}
