.articles-gen {
  max-width: 954px;
  width: 74.53%;

  @include vp--1023 {
    max-width: 100%;
    width: 100%;
  }
}

.articles-gen__pagination {
  margin: 0;

  @include vp--1023 {
    justify-content: space-between;
  }
}

.articles-gen__pagination .pagination__item {
  @include vp--1023 {
    margin-left: 0;

    &--mobile {
      display: block;
    }
  }
}

.articles-gen__article {
  display: flex;
  margin: 24px 0;

  @include vp--1023 {
    flex-direction: column;
  }
}

.articles-gen__img {
  width: 31.65%;

  & img {
    width: 100%;
    background-color: rgba(11, 11, 11, 0.16);
    object-fit: cover;
  }

  @include vp--1023 {
    width: 100%;
    margin-bottom: 16px;
  }
}

.articles-gen__controls {
  position: relative;
  border-bottom: 2px solid $color-whisper;
}

.articles-gen__controls .tab {
  @include vp--767 {
    font-size: 16px;
    line-height: 24px;
  }
}

.articles-gen__wrapper {
  width: 100%;
}

.articles-gen__content {
  margin-left: 24px;
  width: 65.82%;

  & time {
    color: $color-dark-gray;
  }

  & h3 {
    margin: 8px 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
  }

  & p {
    margin: 0;
    margin-bottom: 16px;
    color: $color-charcoal;
  }

  @include vp--1023 {
    margin: 0;
    width: 100%;
  }
}

.articles-gen__link {
  @include vp--1023 {
    width: 100%;
    justify-content: center;
  }
}
