/* stylelint-disable value-no-vendor-prefix */
/* stylelint-disable property-no-vendor-prefix */
.comparison {
  margin-bottom: 120px;
  position: relative;

  @include vp--767 {
    margin-bottom: 32px;
  }
}

.comparison__scrollbar {
  opacity: 1;
  top: 201px;
  position: relative;
}

.comparison__scrollbar .ps__thumb-x {
  background-color: $color-apple;
  top: -2px;
}

.comparison__scrollbar.ps--clicking .ps__thumb-x,
.comparison__scrollbar:focus > .ps__thumb-x,
.comparison__scrollbar:hover > .ps__thumb-x {
  background-color: $color-apple;
  height: 6px;
}

.ps--focus > .comparison__scrollbar,
.ps--scrolling-x > .comparison__scrollbar,
.ps:hover > .comparison__scrollbar {
  opacity: 1;
}

.ps--active-x > .comparison__scrollbar {
  background-color: $color-whisper;
  height: 2px;
}

.comparison__tabs {
  border-bottom: 2px solid $color-whisper;

  display: flex;
  padding-right: 20px;
}

.comparison__reset {
  background-color: $color-transparent;
  border: none;
  color: $color-apple;
  cursor: pointer;

  margin-left: auto;
  padding: 0;

  & svg {
    margin-left: 11px;
  }
}

.comparison__desktop {
  @include vp--767 {
    display: none;
  }
}

.comparison__table {
  text-align: left;

  display: flex;
  min-width: 528px;
  position: relative;

  & .comparison__name {
    font-size: 16px;
  }

  @include vp--767 {
    font-size: 14px;
  }
}

.comparison__name {
  font-weight: 400;
  margin: 8px 0;

  @include vp--767 {
    line-height: 20px;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
}

.comparison__icon {
  position: absolute;
  border: none;
  background-color: $color-transparent;
  color: $color-very-light-grey;
  right: 40px;
  cursor: pointer;
}

.comparison__price {
  font-weight: 500;
}

.comparison__subtitle--active {
  color: $color-nero;
}

.comparison__row {
  display: flex;
}

.comparison__cell {
  padding: 24px 0;
  width: 25.46%;
  padding-right: 24px;
  border-top: $color-whisper solid 1px;

  &:last-child {
    padding-right: 0;
    width: 23.59%;
  }

  @include vp--767 {
    padding: 16px 0;
    padding-right: 16px;
    width: 27.27%;

    &:last-child {
      padding-right: 0;
      width: 24.24%;
    }
  }
}

.comparison__cell--no-border {
  border: none;
}

.comparison__cell--name {
  display: flex;
  flex-direction: column;
  position: relative;
  border: none;
}

.comparison__cell--mobile-small {
  @include vp--767 {
    width: 21.21%;
    padding-right: 16px;
  }
}

.comparison__link {
  color: $color-apple;

  &:hover {
    color: $color-olivine;
  }
}

.comparison__arrow {
  display: none;
  background-color: $color-transparent;
  border: none;
  color: $color-very-light-grey;

  @include vp--767 {
    position: absolute;
    top: 321px;
    right: 0;
    display: flex;
    align-items: center;
  }
}

.comparison__arrow--left {
  transform: rotate(180deg);
  left: 0;
}
