.button {
  @include non-touch {
    &:hover {
      background-color: $color-olivine;
    }

    &:disabled:hover {
      background-color: $color-whisper;
    }

    &--theme-border:hover {
      background-color: $color-default-white;
      border: 1px solid $color-apple;
    }

    &--theme-border:hover:disabled {
      background-color: $color-default-white;
      border-color: $color-whisper;
    }

    &--theme-simpler:hover {
      background-color: $color-panache;
    }

    &--theme-simpler:hover:disabled {
      color: $color-whisper;

      background-color: transparent;
    }

    &--theme-burger:hover {
      color: $color-tacao;

      background-color: transparent;
    }

    &--theme-burger {
      background-color: transparent;
    }

    &--theme-orange:hover {
      color: $color-safety-orange;

      background-color: $color-seashell;
    }

    &--theme-tag:hover {
      background-color: $color-default-white;
      border-color: $color-apple;
    }

    &--color {
      background-color: transparent;
    }

    &--icon-only:hover {
      background-color: transparent;
    }

    &--icon-only:hover .button__icon {
      opacity: 0.7;
    }

    &--decor:hover {
      color: $color-apple;

      background-color: transparent;
      border-color: $color-apple;
      opacity: 0.8;
    }

    &--decor-color:hover {
      color: $color-olivine;

      background-color: transparent;
      border-color: $color-olivine;
    }

    &--theme-orange-text:hover {
      color: $color-white;

      background-color: $color-safety-orange;
      border-color: $color-safety-orange;
    }

    &--theme-orange-text:hover:disabled {
      color: $color-whisper;

      background-color: $color-default-white;
    }

    &--color-gray:hover {
      color: $color-sun;

      background-color: transparent;
    }

    &--theme-olive:hover {
      color: $color-default-white;

      background-color: $color-apple;
    }

    &--no-bg:hover {
      background-color: transparent;
      border: none;
      opacity: 0.8;
    }

    &--theme-orange-tag:hover {
      color: $color-white;

      background-color: $color-safety-orange;
      border-color: $color-safety-orange;
    }
  }

  position: relative;

  display: inline-flex;
  align-items: center;
  padding: 8px 16px;

  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: $color-default-white;

  background-color: $color-apple;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  transition: $trans--300;
  transition-property: background-color, border-color, color;

  &--theme-burger {
    padding: 0;
  }

  &--open .button__icon-close {
    display: none;
  }

  &--close .button__icon-open {
    display: none;
  }

  &:disabled {
    background-color: $color-whisper;
    cursor: not-allowed;
  }

  &--icon-left .button__icon {
    margin-right: 9px;
  }

  &--icon-right .button__icon {
    order: 10;
    margin-left: 9px;
  }

  &--decor {
    padding: 0;

    color: $color-charcoal;

    background-color: transparent;
    border-bottom: 1px dashed $color-charcoal;
    border-radius: 0;
  }

  &--decor-color {
    padding: 0;

    color: $color-apple;

    background-color: transparent;
    border-bottom: 1px dashed $color-apple;
    border-radius: 0;
  }

  &--theme-border {
    color: $color-apple;

    background-color: $color-default-white;
    border: 1px solid $color-madang;
  }

  &--theme-border:disabled {
    color: $color-whisper;

    background-color: $color-default-white;
    border: 1px solid $color-whisper;
  }

  &--theme-simpler {
    color: $color-apple;

    background-color: transparent;
  }

  &--theme-simpler:disabled {
    color: $color-whisper;

    background-color: transparent;
  }

  &--theme-orange {
    padding: 4px 16px;

    font-size: 14px;

    background-color: $color-safety-orange;
    border-radius: 40px;
  }

  &--theme-orange-text {
    color: $color-safety-orange;

    background-color: transparent;
    border-color: $color-tacao;
  }

  &--theme-orange-tag {
    padding: 4px 16px;
    padding-right: 21px;

    font-size: 14px;

    background-color: $color-seashell;
    border-radius: 40px;
    color: $color-safety-orange;
  }

  &--theme-burger::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: calc(100% + 20px);
    height: calc(100% + 20px);

    transform: translate(-50%, -50%);
  }

  &--theme-tag {
    padding-top: 3px;
    padding-bottom: 3px;

    color: $color-apple;

    background-color: $color-panache;
    border: 1px solid $color-madang;
    border-radius: 40px;
  }

  &--icon-only {
    padding: 0;

    color: $color-dark-gray;

    background: transparent;
  }

  &--icon-only .button__icon {
    transition: $trans--300;
    transition-property: opacity;
  }

  &--color {
    color: $color-safety-orange;

    background-color: transparent;
  }

  &--color-gray {
    color: $color-very-light-grey;

    background-color: transparent;
  }

  &--not-paddind {
    padding: 0;
  }

  &--increase-clickable {
    position: relative;
  }

  &--increase-clickable::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: calc(100% + 10px);
    height: calc(100% + 10px);

    transform: translate(-50%, -50%);
  }

  &--theme-olive {
    color: $color-apple;

    background-color: transparent;
  }

  &--horizontal-align-center {
    justify-content: center;
  }

  &--grey {
    color: $color-dark-gray;
  }

  &--no-bg {
    padding: 0;

    background-color: transparent;
    border: none;
    border-radius: 0;
  }

  &--arrow {
    display: inline-flex;
    align-items: center;
    padding: 8px 16px;

    background-color: transparent;
    border-radius: 4px;
    color: $color-apple;

    transition: $trans--300;
    transition-property: background-color, color;

    svg {
      margin-left: 10px;
    }

    &:hover,
    &:focus,
    &:active {
      background-color: $color-apple;
      color: $color-default-white;
    }
  }
}
