.modal-testimonial {
  font-size: 14px;
  color: $color-dark-gray;

  .modal__content {
    min-width: 629px;
  }

  h3 {
    margin-top: 0;
    margin-bottom: 16px;

    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: $color-nero;
  }

  p {
    margin: 0;
    font-size: 16px;
    color: $color-charcoal;
  }
}

.modal-testimonial__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  sup {
    color: $color-red-orange;
  }

  textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 16px;

    border: 1px solid $color-whisper;
    border-radius: 4px;
    color: $color-nero;
  }
}

label[data-drop-area="testimonial"] {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 12px;
  margin-bottom: 16px;

  font-size: 16px;
  color: $color-apple;

  border: 1px dotted $color-apple;
  border-radius: 8px;
  cursor: pointer;

  @include non-touch {
    &:hover {
      opacity: 0.8;
    }
  }
}
