.writing-button {
  display: flex;
  align-items: center;
  margin-bottom: 24px;

  .writing-button__button {
    flex-grow: 0;
    margin-right: 24px;
  }

  .info-message {
    display: flex;
    align-items: center;
    padding: 0;

    background-color: transparent;

    svg {
      margin-right: 9px;
    }

    p {
      margin: 0;
      font-size: 14px;
    }
  }
}
