@include non-touch {
  .header-middle .header-middle__catalog:hover {
    @include vp--1023 {
      color: $color-apple;
    }
  }

  .header-middle .header-middle__catalog {
    .link:hover {
      color: $color-apple;
    }
  }

  .header-bottom__brands:hover {
    @include vp--1023 {
      color: $color-apple;
    }
  }

  .header-middle .input-field input:hover {
    @include vp--1023 {
      box-shadow: inset 0 0 0 4px $color-tacao;
    }
  }

  .header-middle .header-link-list .link:hover {
    @include vp--1023 {
      color: $color-apple;
    }

    color: $color-apple;
  }

  .header-middle .header-bottom__brands:hover {
    @include vp--1023 {
      color: $color-apple;
    }
  }

  .header-middle__logo[href]:hover {
    cursor: pointer;
    opacity: 0.5;
  }
}

.header-middle .container {
  @include vp--1023 {
    grid-template-columns: repeat(3, 1fr);
    row-gap: 0;
  }

  display: grid;

  grid-template-columns: minmax(auto, 243px) minmax(auto, 134px) minmax(auto, 685px)  1fr;
  row-gap: 24px;
}


.header-middle .logo img {
  @include vp--1023 {
    max-width: 114px;
    max-height: 24px;
  }

  max-width: 190px;
  max-height: 40px;

  object-fit: cover;
}

.header-middle .header-middle__order-where {
  @include vp--1023 {
    display: none;
  }

  position: relative;

  padding-bottom: 9px;

  font-size: 13px;
  line-height: 20px;

  grid-column: 1 / 2;
}

.header-middle .header-middle__order-where::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -100vw;

  width: 200vw;
  height: 1px;

  background-color: $color-white-smoke;
}

.header-middle__wrap-info {
  @include vp--1023 {
    flex-direction: column;
    padding-bottom: 0;

    grid-column: 1 / -1;
    grid-row: 2;
  }

  display: flex;
  justify-content: flex-end;
  padding-bottom: 9px;

  grid-column: -1 / -4;
}

.header-middle .header-middle__phone {
  @include vp--1023 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 0;
    margin-bottom: 16px;

    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
  }

  margin-right: 32px;

  font-size: 13px;
  line-height: 20px;
}

.header-middle .header-mobile-item {
  @include vp--1023 {
    position: relative;

    padding-top: 16px;
    padding-right: 0;
    padding-bottom: 16px;
    padding-left: 0;

    color: $color-nero;

    background-color: transparent;
    border: none;
    box-shadow: inset 0 1px $color-whisper;
  }
}

.header-middle .header-mobile-item::before {
  @include vp--1023 {
    content: "";
    position: absolute;
    top: 0;
    left: -50px;

    width: 200vh;
    height: 1px;

    box-shadow: inset 0 1px $color-whisper;
  }
}

.header-middle .header-middle__phone span {
  @include vp--1023 {
    font-weight: 400;
    // font-size: 14px;
    line-height: 24px;
    color: $color-dark-gray;
  }
}

.header-middle .header-middle__opening-hours {
  @include vp--1023 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 0;

    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
  }

  margin-top: 0;
  margin-bottom: 0;

  font-size: 13px;
  line-height: 20px;
}

.header-middle .header-middle__opening-hours span {
  @include vp--1023 {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $color-dark-gray;
  }
}

.header-middle__logo {
  @include vp--1023 {
    margin-bottom: 16px;

    grid-column: 2 / 3;
    grid-row: 1 / 2;
    justify-self: center;
  }

  transition: $trans--300;
  transition-property: opacity;

  grid-column: 1 / 2;
  grid-row: 2 / 3;
  justify-self: flex-start;
}

.header-middle .header-link-list {
  @include vp--1023 {
    flex-direction: column;

    grid-column: 1 / -1;
    grid-row: 8;
  }

  position: relative;

  align-self: center;

  font-weight: 500;
  color: $color-nero;

  grid-column: 2 / 4;
  grid-row: 2 / 3;
}

.header-middle .header-link-list .link {
  @include vp--1023 {
    flex-grow: 1;
  }

  font-weight: 500;
  color: $color-nero;
}

.header-middle .header-link-list .header-link-list__item {
  @include vp--1023 {
    display: flex;
    justify-content: stretch;
  }

  position: relative;

  justify-content: flex-end;
}

.header-middle .header-middle__login {
  @include vp--1023 {
    display: none;
  }

  display: flex;
  flex-grow: 0;
  justify-content: flex-end;
  align-self: center;
  margin-left: 24px;

  grid-column: -1 / -2;
  justify-self: flex-start;
}

.header-middle .header-middle__catalog {
  @include vp--1023 {
    min-width: auto;

    grid-column: 1 / -1;
    grid-row: 4;
  }

  min-width: 234px;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 26px;

  text-align: center;

  background-color: $color-apple;
  border-right: 1px solid rgba($color-default-white, 0.3);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;

  grid-column: 1 / 2;
  grid-row: 3 / 4;
}

.header-middle .header-middle__catalog .button {
  @include vp--1023 {
    display: none;
  }
}

.header-bottom__brands {
  @include vp--1023 {
    grid-column: 1 / -1;
    grid-row: 5;
  }

  padding-top: 16px;
  padding-right: 24px;
  padding-bottom: 16px;
  padding-left: 24px;

  background-color: $color-apple;

  grid-column: 2 / 3;
  grid-row: 3 / 4;
}

.header-middle .header-middle__seach {
  @include vp--1023 {
    margin-top: 16px;
    padding-top: 0;
    padding-bottom: 0;

    background-color: transparent;

    grid-column: 1 / -1;
    grid-row: 10;
  }

  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 4px;
  padding-bottom: 4px;

  background-color: $color-apple;

  grid-column: 3 / 4;
  grid-row: 3 / 4;
}

.header-middle .drop-down-seach {
  position: absolute;
  top: 52px;
  z-index: 10;

  width: 100%;
}

.header-middle .input-field input {
  @include vp--1023 {
    border-radius: 8px;
    box-shadow: inset 0 0 0 4px $color-apple;
  }
}

.header-middle .input-field input:focus {
  @include vp--1023 {
    border-radius: 8px;
    box-shadow: inset 0 0 0 4px $color-tacao;
  }
}

.header-middle .header-middle-basket {
  @include vp--1023 {
    display: none;
  }

  display: flex;
  min-width: 222px;
  padding-top: 10px;
  padding-right: 27px;
  padding-bottom: 10px;
  padding-left: 24px;

  background-color: $color-apple;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;

  grid-column: -1 / -2;
  grid-row: 3 / 4;
}

.header-middle .link .link__icon {
  @include vp--1023 {
    flex-shrink: 0;
    margin-left: auto;
  }
}

.header-middle .header-middle__button-burger {
  @include vp--1023 {
    display: flex;

    grid-column: 1 / 2;
    grid-row: 1 / 2;
    justify-self: flex-start;
  }

  display: none;
}

.header-middle__button-burger.button--open ~ * {
  @include vp--1023 {
    display: none;
  }
}

.header-middle .header-middle__phone-button {
  @include vp--1023 {
    display: flex;

    justify-self: flex-end;
  }

  display: none;
}

.button--open + .header-middle__wrap-info {
  @include vp--1023 {
    display: none;
  }
}

.header-middle .button--close {
  @include vp--1023 {
    color: $color-dark-gray;
  }
}

.header-middle__phone-button.button {
  color: $color-dark-gray;
}

.header-link-list__popap {
  @include vp--1023 {
    position: static;

    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: stretch;
    width: auto;
    height: auto;
    margin: unset;
    padding: 0;

    border-top: none;
    box-shadow: none;
    transform: none;
    opacity: 1;

    clip: auto;
  }

  position: absolute;
  right: -14px;
  bottom: 0;

  width: 0;
  height: 0;
  margin: -1px;
  padding-top: 14px;
  padding-right: 22px;
  padding-bottom: 5px;
  padding-left: 14px;

  background-color: $color-default-white;
  border-top: 1px solid $color-olivine;
  box-shadow: 0 30px 30px rgba(0, 0, 0, 0.1);
  transform: translateY(calc(100% + 10px));
  opacity: 0;

  transition: $trans--300;
  transition-property: opacity;

  clip: rect(0 0 0 0);
}

.link--menu-close + .header-link-list__popap {
  width: auto;
  height: auto;
  margin: auto;

  opacity: 1;

  clip: auto;
}

.header-middle .header-link-list__popap .header-mobile-item {
  @include vp--1023 {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  font-weight: 400;
  // font-size: 14px;
  line-height: normal;
}

.header-link-list__popap .header-mobile-item + .header-mobile-item {
  @include vp--1023 {
    margin-top: 0;
  }

  margin-top: 10px;
}

.header-middle .header-link-list .header-link-list__link-more {
  @include vp--1023 {
    display: none;
  }
}
