.tab {
  @include non-touch {
    &:hover {
      color: $color-nero;
    }
  }

  position: relative;
  padding: 16px 16px 14px;
  border: none;
  font-family: $font-family--main;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  white-space: nowrap;
  color: $color-dark-gray;
  background-color: transparent;
  transition: color $trans--300;
  cursor: pointer;

  &--size-m {
    font-size: 16px;
  }

  &--short {
    padding: 16px 8px 14px;
  }

  &--sm {
    padding: 16px 8px;
  }

  &--padding-m {
    padding: 12px 16px 10px;
    font-size: 16px;
  }
}

.tab--active {
  color: $color-nero;

  &::before {
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $color-apple;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
  }
}
