@include non-touch {
  .modal--omega .modal__close-btn:hover {
    color: $color-sun;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans--600,
    transform $trans--600,
    visibility $trans--600;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp--767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    background-color: $color-default-white;

    @include vp--767 {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 15px;
    right: 15px;

    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content .modal__content {
    width: auto;

    @include vp--767 {
      width: 100%;
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

  &--order-where .modal__content {
    @include vp--767 {
      width: auto;
    }
  }
}

.modal--omega {
  background-color: rgba($color-nero, 0.6);

  & .modal__content {
    padding: 24px;

    border-radius: 8px;

    @include vp--767 {
      padding: 16px;

      box-shadow: 0 16px 32px rgba($color-default-black, 0.1);
    }
  }

  & .modal__close-btn {
    @include vp--767 {
      top: 21px;
      right: 21px;
    }

    top: 33px;
    right: 29px;

    margin: -10px; // увеличиваниет кликабельную зоны
    padding: 10px; // увеличиваниет кликабельную зоны

    color: $color-dark-gray;

    transition: $trans--300;
    transition-property: color;
  }

  & .modal__title {
    margin: 0;
    margin-bottom: 16px;

    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: $color-nero;

    @include vp--767 {
      font-size: 18px;
      line-height: 24px;
    }
  }
}

.modal--only-top-padding {
  & .modal__content {
    padding: 0;
    padding-top: 24px;

    @include vp--767 {
      padding: 0;
      padding-top: 16px;
    }
  }

  & .modal__title {
    margin-left: 24px;

    @include vp--767 {
      margin-left: 16px;
    }
  }
}
