.feedback {
  border: 1px solid $color-whisper;
  border-radius: 8px;
  padding: 24px;
  max-width: 954px;

  @include vp--767 {
    width: 100%;
    border: none;
    padding: 0;
  }
}

.feedback__text {
  margin: 0;
  margin-bottom: 16px;
  color: $color-charcoal;
}

.feedback__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.feedback__textarea {
  border: 1px solid $color-whisper;
  border-radius: 4px;
  margin-bottom: 24px;
  padding: 16px;
  min-height: 144px;
  width: 100%;

  &::placeholder {
    color: $color-dark-gray;
    line-height: 24px;
  }

  @include vp--767 {
    margin-bottom: 16px;

    &::placeholder {
      font-size: 14px;
      line-height: 20px;
    }
  }

  @include non-touch {
    &:hover {
      border-color: $color-tacao;
    }

    &:focus {
      outline: none;
      border-color: $color-tacao;
    }
  }
}

.feedback .feedback__submit {
  display: block;
  text-align: center;
  min-width: 286px;

  @include vp--767 {
    width: 100%;
  }
}
