@include non-touch {
  .tabs-flags .button:hover {
    background-color: transparent;
    opacity: 1;
  }
}

.tabs-flags {
  display: flex;
}

.tabs-flags .button {
  padding: 8px;

  background-color: transparent;
  opacity: 0.5;

  transition: $trans--300;
  transition-property: opacity;
}

.tabs-flags .tab--active {
  opacity: 1;
}

.tabs-flags .tab--active::before {
  bottom: -21px;
}

.tabs-flags .tab--active:hover {
  opacity: 0.5;
}
