.popup-basket {
  background-color: $color-white;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  box-shadow: 0 0 10px 0 $color-very-light-grey;
}

.popup-basket__wrap-bottom {
  display: flex;
  align-items: center;
  padding: 16px;
  padding-right: 100px;
}

.popup-basket__wrap-bottom .button + .button {
  margin-left: 8px;
}

.popup-basket__text {
  margin-left: auto;
}
