.quantity-counter {
  display: flex;
  align-items: center;
  padding: 8px 12px;

  text-align: center;

  &--theme-green,
  &--theme-green input {
    background-color: $color-panache;
  }
}

.quantity-counter .button {
  width: 24px;
  height: 24px;
}

.quantity-counter__value {
  width: 24px;
  min-height: 24px;
  margin-right: 8px;
  margin-left: 8px;

  outline: none;
  text-align: center;
  border: none;
}
