.brands__content {
  padding-bottom: 120px;
}

.brands .tabs-flags .tab--active::before {
  bottom: -13px;
}

.brands__alphabet-list {
  margin-right: 25px;
  flex-grow: 1;
  display: flex;
}

.brands__wrapper {
  display: flex;
  align-items: center;
  padding: 12px 24px;
  margin-bottom: 24px;

  border: 1px solid $color-whisper;
  border-radius: 8px;

  &:last-child {
    margin-bottom: 0;
  }
}

.brands .tabs-flags {
  margin-right: 24px;
}

.brands .brands__alphabet {
  position: static;

  &--active {
    display: flex;
  }
}

.brands .alphabet {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.brands .is-active {
  position: static;

  width: auto;
  height: auto;
  margin: 0;

  clip: auto;
}

.brands__wrapper--column {
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
}

.brands__title {
  border-bottom: 1px solid $color-whisper;
  align-self: stretch;
  padding: 0 24px;

  & h2 {
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    margin: 24px 0;

    @include vp--1023 {
      margin: 16px 0;
    }
  }

  @include vp--1023 {
    padding: 0 16px;
  }
}

.brands__list {
  display: flex;
  align-items: flex-start;
  margin: 0;
  padding: 24px;
  list-style: none;
  width: 100%;
  flex-wrap: wrap;

  @include vp--767 {
    padding: 16px;
    padding-bottom: 0;
  }
}

.brands__logo {
  width: 15.04%;
  height: 132px;
  margin-right: 24px;

  display: flex;
  align-items: center;
  justify-content: center;

  &:nth-child(6n) {
    margin-right: 0;
  }

  @include vp--1023 {
    width: 13.6%;
  }

  @include vp--767 {
    width: 47.28%;
    margin-right: 3.5%;
    height: 128px;
    margin-bottom: 16px;

    &:nth-child(even) {
      margin-right: 0;
    }
  }
}

.brands__link-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;

  & .logo {
    object-fit: cover;
    display: flex;
    height: 100px;
    align-items: center;
  }

  & span {
    text-align: center;
    margin-top: 8px;
    font-size: 14px;
    line-height: 24px;
    color: $color-very-light-grey;
  }

  @include non-touch {
    &:hover span {
      color: $color-nero;
    }
  }
}
