.header-link-list {
  display: flex;
  flex-shrink: 0;
  flex-grow: 1;

  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;
}

.header-link-list .link .link__icon {
  margin-left: 6px;
  width: 8px;
  height: 5px;
}

.header-link-list__item {
  display: flex;
  flex-grow: 1;
}

.header-link-list .link {
  font-size: 16px;
  line-height: 24px;
}
