.modal-order-where {
  display: flex;
  flex-direction: column;
  max-width: 362px;
}

.modal-order-where > * + * {
  margin-top: 16px;
}

.modal-order-where__title {
  margin-top: 0;
  margin-bottom: 0;

  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
}

.modal-order-where .button {
  justify-content: center;
}

.modal-order-where__captha {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 74px;

  @include vp--767 {
    width: auto;
  }

  color: $color-very-light-grey-2;

  background-color: $color-white-smoke-4;
}
