.sort-order__wrapper {
  display: flex;

  p {
    margin: 0;
    margin-right: 24px;
    color: $color-dark-gray;
  }
}

.sort-order .sort-order__button {
  margin-right: 24px;

  &--active {
    color: $color-apple;
    border-color: $color-apple;
    cursor: default;

    &:hover {
      opacity: 1;
    }
  }
}
