@include non-touch {
  .alphabet .button:hover {
    color: $color-apple;

    background-color: transparent;
  }

  .alphabet__character:hover {
    color: $color-apple;
  }
}

.alphabet {
  display: flex;
  flex-grow: 1;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;
}

.alphabet__item {
  display: flex;
  flex-grow: 1;
  max-width: 24px;
}

.alphabet__item + .alphabet__item {
  margin-left: 8px;
}

.alphabet__character {
  flex-grow: 1;

  font-weight: 500;
  text-align: center;

  cursor: pointer;
  color: $color-nero;
  transition: $trans--300;
  transition-property: color;
}

.alphabet__item input:checked + .alphabet__character {
  color: $color-apple;
}
