.footer {
  min-height: 100px;
  padding-top: 40px;
  padding-bottom: 24px;

  background-color: $color-apple;
  color: $color-default-white;

  @include vp--1023 {
    padding: 0;
  }
}

.footer__wrapper {
  max-width: calc(1280px + 40px * 2);
  margin: 0 auto;
  padding: 0 40px;

  @include vp--1023 {
    padding: 0;
  }
}

.footer__top {
  display: flex;
  position: relative;
  padding-bottom: 8px;

  &::after {
    content: "";
    width: 100%;
    height: 2px;
    display: block;
    position: absolute;
    bottom: 0;
    background-color: $color-olivine;
    opacity: 0.3;
  }

  @include vp--1023 {
    flex-direction: column;
    padding-bottom: 0;
  }
}

.footer__list-wrapper {
  margin-right: 24px;
  width: 256px;
  position: relative;

  &:last-child {
    margin-right: 0;
    width: auto;
    min-width: 163px;
  }

  @include vp--1023 {
    width: 100%;

    &::after {
      content: "";
      width: 100%;
      height: 1px;
      display: block;
      position: absolute;
      bottom: 0;
      background-color: $color-olivine;
      opacity: 0.3;
    }
  }
}

.footer__list-title {
  margin: 0;
  padding: 0;
  padding-bottom: 16px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;

  background-color: $color-transparent;
  border: none;
  color: inherit;

  @include vp--1023 {
    padding: 16px;
    display: block;
    width: 100%;

    text-align: left;

    &::after {
      content: "";
      width: 16px;
      height: 16px;
      display: block;
      position: absolute;
      right: 16px;
      top: 20px;
      background-image: url("../img/svg/arrow.svg");
    }
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp--1023 {
    padding-left: 16px;
  }
}

[data-accordion="element"].is-open > .footer__list-title::after {
  transform: rotate(180deg);
}

[data-accordion="element"].is-open > .footer [data-accordion="content"] {
  @include vp--1023 {
    max-height: 100%;
  }
}

.footer [data-accordion="parent"].no-transition [data-accordion="content"] {
  @include vp--1023 {
    transition: none;
  }
}

.footer [data-accordion="content"] {
  @include vp--1023 {
    max-height: 0;
    transition: max-height 0.3s;
    overflow: hidden;
  }
}

.footer__item {
  margin-bottom: 16px;

  a {
    font-size: 16px;
  }
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 22px;

  @include vp--1023 {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 16px;
  }
}

.footer__copyright {
  @include vp--1023 {
    margin-bottom: 8px;
  }
}

.footer__copyright-text {
  margin: 0;

  @include vp--1023 {
    color: $color-madang;
  }
}

.footer__agreement {
  color: $color-default-white;

  @include non-touch {
    &:hover {
      color: $color-tacao;
    }
  }
}

.footer__socials {
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
}

.footer__social {
  width: 40px;
  height: 40px;
  margin-right: 16px;

  display: flex;
  align-items: center;
  justify-content: center;

  &:last-child {
    margin-right: 0;
  }

  & .footer__link {
    color: $color-madang;
    display: flex;
    padding: 10px;
  }
}
