.pagination {
  display: flex;
  padding: 0;

  list-style: none;

  @include vp--767 {
    justify-content: space-between;
  }
}

.pagination__item {
  margin-left: 8px;

  &:first-child {
    margin-left: 0;
  }

  &--mobile {
    display: none;
  }

  @include vp--767 {
    margin-left: 0;

    &--mobile {
      display: block;
    }
  }
}

.pagination__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  min-height: 40px;

  color: $color-nero;

  border-radius: 4px;

  cursor: pointer;

  @include non-touch {
    &:hover:not(&--active) {
      background-color: $color-white-smoke;
    }
  }

  &--active {
    color: $color-default-white;
    background: $color-apple;
  }
}
