.orders {
  display: flex;
  align-items: flex-start;

  @include vp--1023 {
    flex-direction: column;
  }
}

.orders__wrapper {
  max-width: 954px;
  flex-grow: 1;

  @include vp--1023 {
    width: 100%;
  }
}

.orders__order {
  border: 1px solid $color-whisper;
  border-radius: 8px;
  margin-bottom: 24px;
  padding-top: 24px;
  padding-bottom: 24px;

  @include vp--1023 {
    padding: 16px 0;
  }

  @include vp--767 {
    padding: 16px;
    margin-bottom: 0;
  }
}

.orders__order--center {
  text-align: center;

  @include vp--767 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.orders__text {
  margin: 0;
  margin-bottom: 16px;

  font-size: 18px;
  line-height: 32px;

  @include vp--767 {
    width: 60.2%;
  }
}

.orders__button svg {
  margin-left: 10px;
}

.orders__name-wrapper {
  background-color: $color-transparent;
  border: 0;
  padding-left: 24px;
  padding-right: 24px;
  width: 100%;
  text-align: left;

  @include vp--767 {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.orders__name {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;

  margin: 0;
  margin-bottom: 8px;
  cursor: pointer;
  position: relative;

  &::after {
    content: "";
    width: 16px;
    height: 16px;
    display: block;
    position: absolute;
    right: 16px;
    top: 0;
    background-image: url("../img/svg/arrow.svg");
  }

  @include vp--767 {
    &::after {
      display: none;
    }
  }

  @include non-touch {
    &:hover {
      opacity: 0.8;
    }
  }
}

.orders .orders__details {
  display: none;
  width: 100%;

  @include vp--767 {
    display: block;
    margin-top: 16px;
    text-align: center;
  }
}

.orders__title {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  margin-bottom: 8px;

  @include vp--767 {
    margin-bottom: 16px;
  }
}

.orders__title-mobile {
  margin: 0;

  @include vp--767 {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
  }
}

.orders__gray-title {
  font-size: 14px;
  line-height: 16px;
  color: $color-dark-gray;
}

.orders__status-line {
  display: flex;
  align-items: baseline;

  & .info-message {
    margin-left: 8px;
  }

  & p {
    margin: 0;
  }

  @include vp--767 {
    flex-wrap: wrap;
  }
}

.orders__content--closed {
  @include vp--767 {
    display: none;
  }
}

.orders__payment-btn {
  margin-left: auto;
  margin-right: 8px;

  @include vp--767 {
    margin: 0;
    margin-top: 16px;
    padding-top: 4px;
    padding-bottom: 4px;
  }
}

.orders__pay {
  @include vp--767 {
    margin-top: 16px;
    font-size: 14px;
    padding-top: 4px;
    padding-bottom: 4px;
  }
}

[data-accordion="element"].is-open .orders__name::after {
  transform: rotate(180deg);
}

[data-accordion="element"].is-open > .orders [data-accordion="content"] {
  @include vp--768 {
    max-height: 100%;
  }
}

.orders [data-accordion="parent"].no-transition [data-accordion="content"] {
  @include vp--768 {
    transition: none;
  }
}

.orders [data-accordion="content"] {
  @include vp--768 {
    max-height: 0;
    transition: max-height 0.3s;
    overflow: hidden;
  }
}

.orders__parameters {
  border-bottom: 1px solid $color-whisper;
  padding: 24px;

  &:last-child {
    padding-bottom: 0;
  }

  @include vp--767 {
    padding: 16px;
  }
}

.orders__parameters--no-border {
  border: none;
}

.orders .orders__show-more {
  text-align: center;
  width: 100%;
  display: block;
}

.orders__info {
  @include vp--767 {
    margin-right: 8px;
    font-size: 14px;
  }
}

.orders__hide {
  display: none;
}

.orders__payment-wrapper {
  width: 100%;
}

.orders__info--nowrap {
  white-space: nowrap;
}

.orders__info--mr {
  margin-right: 8px;
}

.orders__modal-content {
  width: 410px;

  @include vp--767 {
    width: 100%;
  }

  & .orders__save-btn {
    width: 100%;
    display: block;
    text-align: center;
    padding: 12px 0;
  }
}

.orders__modal .modal__title {
  font-size: 18px;
}

.orders__radio {
  background-color: $color-white-smoke;
  padding: 16px;
  padding-left: 18px;
  margin-bottom: 16px;
  border-radius: 8px;
  font-weight: 500;

  & p {
    margin: 0;
    margin-left: 48px;

    color: $color-dark-gray;
    font-weight: 400;
  }

  @include vp--767 {
    padding: 12px;
    padding-left: 14px;

    & p {
      font-size: 14px;
    }
  }
}

.orders__order--no-border {
  @include vp--767 {
    border: none;
    padding: 0;
  }
}
