.user-question {
  display: flex;
  padding: 20px 0;
}

.user-question__inner {
  width: 100%;
}

.user-question__img {
  min-width: 48px;
  margin-right: 16px;
}

.user-question__header {
  margin-bottom: 0;
}

.user-question__name-autor {
  margin-right: 16px;

  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.user-question__date {
  color: $color-very-light-grey;
}

.user-question__content {
  color: $color-charcoal;

  p {
    margin: 0;
  }
}

.user-question__answer {
  position: relative;
  padding: 16px;
  margin-top: 24px;

  background-color: $color-white-smoke-2;

  border-radius: 0 8px 8px 8px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: -8px;
    width: 0;
    height: 0;

    border-color: transparent transparent $color-white-smoke-2 $color-white-smoke-2;
    border-style: solid;
    border-width: 4px;
  }

  h3 {
    margin: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  p {
    margin: 0;
  }
}

.user-question__footer {
  display: flex;
  align-items: center;
  margin-top: 20px;

  button {
    margin-right: 24px;
    font-weight: normal;
  }

  @include non-touch {
    button:hover {
      color: $color-nero;
    }
  }
}

.user-question__remove {
  svg {
    margin-right: 10px;
  }
}
