.info-message {
  @include vp--767 {
    padding: 12px;

    font-size: 14px;
    line-height: 20px;
  }

  display: flex;
  padding: 16px;

  font-size: 16px;
  line-height: 24px;
  color: $color-dark-gray;

  background-color: $color-white-smoke;
  border-radius: 8px;

  &--error {
    color: $color-red-orange;

    background-color: $color-tutu;
  }

  &--success {
    color: $color-apple;

    background-color: $color-panache;
  }

  &--alert {
    color: $color-safety-orange;

    background-color: $color-seashell;
  }

  &--size-small {
    @include vp--767 {
      padding-top: 4px;
      padding-bottom: 4px;
    }

    display: inline-flex;
    padding-top: 4px;
    padding-bottom: 4px;

    font-size: 14px;
    line-height: 24px;

    border-radius: 40px;
  }
}

.info-message__icon {
  @include vp--767 {
    width: 16px;
    height: 16px;
    margin-right: 10px;
  }

  flex-shrink: 0;
  width: 22px;
  height: 22px;
  margin-right: 17px;
}

.info-message__text {
  margin-top: 0;
  margin-bottom: 0;
}
