.comment-rating {
  margin-left: auto;
  display: flex;
  justify-content: center;
}

.comment-rating button {
  display: flex;
  justify-content: center;

  svg {
    margin-right: 5px;
  }

  &:hover {
    opacity: 1;
    color: $color-charcoal;
  }
}

.comment-rating__like {
  margin-right: 14px;
}

.comment-rating__dislike {
  margin-right: 0;
}
