/* stylelint-disable value-no-vendor-prefix */
/* stylelint-disable property-no-vendor-prefix */

.about {
  padding: 40px 0;
}

.about__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 192px auto;
  column-gap: 24px;

  @include vp--767 {
    grid-template-columns: auto;
  }
}

.about__title {
  margin: 0 0 24px;
  font-size: 32px;
  font-weight: 500;
  line-height: 40px;

  @include vp--767 {
    margin-bottom: 16px;
  }
}

.about__content {
  transition: height $trans--300;

  @include vp--767 {
    height: 240px;
    overflow: hidden;
  }

  &--show {
    @include vp--767 {
      height: auto;
      overflow: auto;
    }
  }
}

.about__logo {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 36px 0;

  @include vp--767 {
    margin-bottom: 16px;
    padding: 32px 0;
  }
}

.about__logo .logo img {
  width: 152px;
  height: 32px;

  @include vp--767 {
    width: 190px;
    height: 40px;
  }
}

.about p {
  margin: 0 0 24px;
  font-size: 16px;
  line-height: 24px;
  color: $color-charcoal;

  &:last-child {
    margin-bottom: 0;
  }

  @include vp--767 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 10;
    -webkit-box-orient: vertical;
  }
}

.about__read-more {
  display: none;
  color: $color-apple;

  @include vp--767 {
    display: inline-block;
    justify-self: end;
  }
}
