.product-details {
  display: flex;
  margin-bottom: 40px;

  .product-details__slider {
    width: percentage(5/9);
    margin-right: 24px;

    @include vp--1023 {
      width: 100%;
      margin-right: 0;
    }
  }

  .product-details__info {
    width: percentage(4/9);

    @include vp--1023 {
      display: none;
    }
  }

  .product-table__row:nth-of-type(n + 7) {
    display: none;
  }
}

.product-details__title {
  margin-top: 23px;
  margin-bottom: 24px;

  font-size: 16px;
  line-height: 24px;
  color: $color-nero;
  font-weight: normal;
}


.product-details__link {
  @include non-touch {
    &:hover {
      opacity: 0.5;
    }
  }
}
