.contacts {
  padding-bottom: 120px;

  & .contacts__toggle {
    margin-bottom: 16px;
  }

  @include vp--767 {
    padding: 0;
  }
}

.contacts__main {
  display: flex;

  @include vp--1023 {
    flex-direction: column;
  }
}

.contacts__content {
  border: 1px solid $color-whisper;
  box-sizing: border-box;
  border-radius: 8px;

  width: 74.55%;
  padding: 24px;

  @include vp--1023 {
    width: 100%;
  }

  @include vp--767 {
    border: none;
    padding: 0;
  }
}

.contacts__top {
  display: flex;
  flex-wrap: wrap;

  @include vp--767 {
    flex-direction: column;
  }
}

.contacts__block {
  margin-bottom: 24px;

  @include vp--767 {
    margin-bottom: 16px;
    width: 100%;
  }
}

.contacts__addres {
  width: 65.92%;
  margin-right: 24px;
}

.contacts__timetable,
.contacts__tel {
  width: 31.63%;
  margin-right: 24px;
}

.contacts__pay {
  width: 31.4%;

  & svg {
    margin-right: 16px;
  }
}

.contacts__mail {
  & a {
    color: $color-apple;
    font-weight: 400;
  }
}

.contacts__title {
  color: $color-dark-gray;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  margin: 0;
  margin-bottom: 8px;

  @include vp--767 {
    margin: 0;
  }
}

.contacts__text {
  margin: 0;

  & a {
    font-weight: 400;
  }
}
