.product-offer {
  width: 100%;
  margin-bottom: 50px;
}

.product-offer__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;

  border-bottom: 2px solid $color-whisper;
  border-radius: 2px 2px 0 0;

  h3 {
    padding: 14px 24px;
    margin: 0;
    margin-bottom: -2px;

    font-weight: 500;
    font-size: 18px;
    line-height: 24px;

    border-bottom: 2px solid $color-apple;
    border-radius: 2px 2px 0 0;
  }
}

.product-offer__controls {
  display: flex;
  align-items: center;
}

.product-offer__btn-prev,
.product-offer__btn-next {
  position: relative;
  width: 40px;
  height: 40px;

  font-size: 0;

  border-radius: 50%;
  cursor: pointer;
  outline: none;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 35%;
    width: 10px;
    height: 10px;

    border-color: $color-very-light-grey $color-very-light-grey transparent transparent;
    border-width: 2px;
    border-style: solid;

    transform: translateY(-50%) rotate(45deg);
  }

  @include non-touch {
    &:hover,
    &:focus {
      background-color: $color-panache;
    }

    &:active {
      opacity: 0.8;
    }

    &:hover::before,
    &:focus::before {
      border-color: $color-apple $color-apple transparent transparent;
    }
  }
}

.product-offer__btn-prev::before {
  left: 45%;
  transform: translateY(-50%) rotate(-135deg);
}

.product-offer__container {
  width: 100%;
  overflow: hidden;

  .swiper-container {
    max-width: 953px;
    width: 100%;
  }
}
