.product-table {
  width: 100%;
  margin-bottom: 8px;

  &__table {
    width: 100%;
    border-spacing: 0 6px;
  }
}

.product-table__cell {
  position: relative;

  font-size: 14px;
  line-height: 24px;

  span {
    position: relative;
    padding-right: 4px;
    background-color: $color-white;
  }

  &:first-of-type {
    width: 52%;
    color: $color-dark-gray;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      top: calc(50% - 1px);
      left: 0;

      background-image: linear-gradient(to right, $color-very-light-grey 25%, rgba(255, 255, 255, 0) 0%);
      background-position: bottom;
      background-size: 4px 1px;
      background-repeat: repeat-x;
    }
  }

  &:last-of-type {
    padding-left: 4px;
    color: $color-charcoal;

    &.product-table__cell--accent {
      color: $color-apple;
    }
  }
}
