.amount {
  display: inline-block;
  min-width: 28px;
  min-height: 24px;
  padding: 0 6px;

  font-size: 14px;
  text-align: center;
  color: $color-dark-gray;

  background-color: $color-default-white;
  border-radius: 24px;

  &--theme-orange {
    color: $color-sun;

    background-color: $color-seashell;
  }
}
