.chapter {
  display: flex;
  flex-direction: column;
  padding: 16px 0;

  background-color: $color-white-smoke-2;

  &--theme-white {
    background-color: $color-default-white;
  }
}

.chapter--small {
  padding: 0;

  & .chapter__item {
    padding: 0 16px;
  }

  & .chapter__title {
    font-weight: normal;
    font-size: 14px;
  }
}

.chapter__item {
  display: flex;
  align-items: center;

  width: 100%;
  padding: 0 16px;

  line-height: 24px;

  background-color: transparent;
  border: none;
  cursor: pointer;
  text-align: left;

  &--auto-pointer {
    cursor: auto;
  }
}

.chapter__title {
  margin: 0;

  font-weight: 500;
  color: $color-nero;

  cursor: pointer;
}

.chapter__reset {
  margin-left: 9px;
  padding: 0;

  font-size: 12px;
  line-height: 24px;
  color: $color-safety-orange;

  background-color: transparent;
  border: none;
  cursor: pointer;
}

.chapter__amount {
  margin-left: auto;
}

.chapter__button {
  position: relative;

  margin-left: auto;
}

.chapter__icon {
  margin-right: 7px;
  margin-left: auto;

  color: $color-very-light-grey;

  cursor: pointer;
  transform: rotate(180deg);
}
