.total-cost {
  padding-top: 24px;
  border: 1px solid $color-whisper;
  border-radius: 8px;
}

.total-cost__info,
.total-cost__take,
.total-cost__warning {
  padding: 0 22px;
  border-bottom: 1px solid $color-whisper;
}

.total-cost__empty {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  margin-bottom: 24px;

  background-color: $color-white-smoke-2;
  color: $color-very-light-grey;
  font-weight: 500;
  font-size: 16px;

  border-radius: 4px;
}

.total-cost__price {
  display: flex;
  align-items: center;
  margin: 0;

  font-weight: 500;
  font-size: 24px;
  line-height: 40px;
  color: $color-nero;

  &--old {
    margin-left: 16px;

    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    color: $color-charcoal;

    text-decoration: line-through;
  }
}

.total-cost__saving {
  display: inline-block;
  padding: 4px 8px;
  margin-bottom: 15px;

  color: $color-white;
  font-size: 13px;
  line-height: 16px;
  vertical-align: middle;
  text-align: center;

  background-color: $color-safety-orange;
  border-radius: 4px;
}

.total-cost__available,
.total-cost__stock {
  margin-bottom: 16px;

  font-size: 13px;
  line-height: 16px;
  color: $color-charcoal;
}

.total-cost__stock {
  position: relative;
  margin-left: 24px;

  svg {
    position: absolute;
    top: 50%;
    left: -22px;

    color: $color-apple;

    transform: translateY(-50%);
  }
}

.total-cost__stock--remote {
  display: flex;
  align-items: center;

  svg {
    color: $color-safety-orange;
  }
}

.total-cost__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.total-cost__trigger {
  display: flex;
  align-items: center;
  cursor: pointer;

  svg {
    position: relative;
    left: 0;
    color: $color-default-black;

    transform: translate(0, 0);
  }
}

.total-cost__trigger:hover {
  & + .total-cost__desc {
    display: block;
  }

  &::before {
    content: "";
    position: absolute;
    width: 150%;
    height: 150%;
    left: 50%;
    top: 50%;

    transform: translate(-50%, -50%);
    z-index: 2;
  }
}

.total-cost__desc {
  display: none;
  position: absolute;
  min-width: 207px;
  bottom: -5px;
  left: 50%;
  padding: 4px 12px;

  background-color: $color-nero;
  opacity: 0.95;
  border-radius: 4px;

  color: $color-white;
  font-size: 14px;
  line-height: 20px;
  text-align: center;

  transform: translate(-50%, 100%);
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    top: -10px;
    left: 50%;
    opacity: 0.95;

    border-style: solid;
    border-width: 5px;
    border-color: transparent transparent $color-nero transparent;

    transform: translateX(-50%);
  }
}

// активные кнопки
.total-cost form {
  display: flex;
  align-items: stretch;
  margin-right: -16px;
  margin-bottom: 23px;

  & div {
    margin-right: 16px;
  }
}

.total-cost__button-count {
  width: 41%;
  display: flex;
  align-items: center;

  & button,
  & span {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    height: 100%;

    background-color: $color-panache;
  }

  & button {
    flex-grow: 2;
  }
}

.total-cost__button-action {
  flex-grow: 1;

  button {
    width: 100%;
    justify-content: center;
  }

  .button {
    border-radius: 4px;
    height: 100%;
    font-size: 16px;
  }
}

// доставка
.total-cost__take {
  padding-left: 60px;
  padding-top: 15px;

  .total-cost__take-wrapper {
    margin-bottom: 16px;
  }

  a {
    position: relative;
    margin-bottom: 3px;

    font-size: 16px;
    line-height: 24px;

    svg {
      position: absolute;
      top: 50%;
      left: -36px;

      transform: translateY(-50%);
    }
  }

  p {
    margin: 0;

    font-size: 14px;
    line-height: 24px;
  }
}

.total-cost__warning {
  font-size: 14px;
  line-height: 20px;
  color: $color-dark-gray;
}
