.gallery-testimonials {
  position: relative;
  margin-bottom: 40px;

  h2 {
    margin-bottom: 10px;

    font-weight: normal;
    color: $color-dark-gray;
    font-size: 16px;
    line-height: 24px;
  }
}

.gallery-testimonials__slider {
  position: relative;
}

.gallery-testimonials-next,
.gallery-testimonials-prev {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  z-index: 10;

  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.05);
  cursor: pointer;
  border-radius: 50%;
  background-color: $color-white;

  transform: translateY(-50%);

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    width: 11px;
    height: 11px;

    border: 2px solid $color-apple;

    transform: translateY(-50%) rotate(45deg);
  }

  &:hover {
    opacity: 0.8;
  }

  &.swiper-button-disabled {
    display: none;
  }
}

.gallery-testimonials-prev {
  left: -20px;
  right: auto;

  &::after {
    left: 40%;
    right: auto;
    border-color: transparent transparent $color-apple $color-apple;
  }
}

.gallery-testimonials-next {
  left: auto;
  right: -20px;

  &::after {
    left: auto;
    right: 40%;
    border-color: $color-apple $color-apple transparent transparent;
  }
}
