.home-gallery {
  margin-bottom: 28px;
}

.home-gallery__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  list-style: none;
  margin: 0;
  margin-left: -6px;
  padding: 0;
}

.home-gallery__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 332px;
  height: 420px;

  background-color: $color-default-white;
  border-radius: 8px;

  pointer-events: none;
  overflow: hidden;

  &--pos-4 {
    z-index: 4;
  }

  &--pos-3 {
    z-index: 3;
  }

  &--pos-2 {
    z-index: 2;
  }

  &--pos-1 {
    z-index: 1;
  }

  &--active {
    z-index: 10;
    pointer-events: auto;
    cursor: pointer;

    &::before {
      display: none;
    }
  }

  &:not(:first-child) {
    margin-left: -230px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color-default-white, 0.4);
  }

  .home-gallery__btn {
    display: none;

    &--active {
      display: block;
      position: absolute;
      top: 13px;
      right: 23px;
    }

    &--reverse {
      transform: rotate(180deg);
    }
  }

  img {
    width: 320px;

    border-radius: 8px;
    object-fit: contain;
  }
}
