.user-testimonial {
  display: flex;
  padding: 20px 0;
}

.user-testimonial__inner {
  width: 100%;
}

.user-testimonial__img {
  min-width: 48px;
  margin-right: 16px;
}

.user-testimonial__header {
  margin-bottom: 20px;

  .rating {
    margin-top: 5px;
  }

  .rating span {
    font-size: 0;
  }
}

.user-testimonial__name-autor {
  margin-right: 16px;

  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.user-testimonial__date {
  color: $color-very-light-grey;
}


.user-testimonial__content {
  color: $color-charcoal;

  p {
    margin-bottom: 20px;
  }
}

.user-testimonial__images {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 20px;
  margin-right: -16px;
  margin-bottom: -20px;

  img {
    margin-right: 16px;
    margin-bottom: 20px;
  }
}

.user-testimonial__footer {
  display: flex;
  align-items: center;

  button {
    margin-right: 24px;
    font-weight: normal;
  }

  @include non-touch {
    button:hover {
      opacity: 1;
      color: $color-charcoal;
    }
  }
}

.user-testimonial__remove {
  svg {
    margin-right: 10px;
  }
}
