.filter {
  padding: 0;

  list-style: none;
}

.filter__chapter {
  margin-bottom: 16px;

  &:first-child {
    border-radius: 8px 8px 0 0;
  }

  &:last-child {
    margin-bottom: 0;

    border-radius: 0 0 8px 8px;
  }
}

.filter__list {
  position: relative;

  max-height: 248px;
  padding-right: 16px;
  padding-left: 18px;
  overflow-y: auto;

  list-style: none;

  & .ps__thumb-y {
    right: 0;

    width: 4px;
  }

  & .ps__rail-y.ps--clicking > .ps__thumb-y,
  & .ps__rail-y:hover > .ps__thumb-y {
    right: 2px;

    width: 11px;
  }
}

.filter__item {
  display: flex;
  margin-bottom: 8px;
}

.filter__item:last-of-type {
  margin-bottom: 0;
}

.filter__range-slider {
  margin: 0;
}


.filter__amount {
  margin-left: auto;
}

.filter [data-accordion="element"].is-open .chapter__icon {
  transform: none;
}

.filter [data-accordion="element"] .chapter__reset {
  display: none;
}

.filter [data-accordion="element"].is-open .chapter__reset {
  display: block;
}

.filter [data-accordion="element"].is-open > [data-accordion="content"] {
  max-height: 100%;
  margin-top: 8px;
}

.filter[data-accordion="parent"].no-transition [data-accordion="content"] {
  transition: none;
}

.filter [data-accordion="content"] {
  max-height: 0;
  overflow: hidden;

  transition: max-height 0.3s;
}

.filter [data-accordion="element"].is-open {
  margin-bottom: 16px;
}
