.home-banner {
  position: relative;
  padding: 32px;
  border-radius: 8px;
  color: $color-nero;
  background-color: $color-white-smoke;

  @include vp--1023 {
    padding: 16px;
    overflow: hidden;
  }
}

.home-banner__title {
  margin: 0 0 8px;
  font-size: 32px;
  font-weight: 500;
  line-height: 40px;

  @include vp--1023 {
    font-size: 24px;
    line-height: 32px;
  }
}

.home-banner__subtitle {
  margin: 0 0 16px;
  font-size: 16px;
  line-height: 24px;
}

.home-banner__image {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: auto;

  @include vp--1023 {
    right: -45px;
  }

  @media (max-width: 340px) {
    right: -60px;
  }
}
