.range-slider__inputs {
  display: flex;
  margin-bottom: 18px;
}

.range-slider__input-field {
  width: 48.7%;

  &:first-child {
    margin-right: auto;
  }
}


.range-slider__target,
.range-slider__target * {
  box-sizing: border-box;

  user-select: none;

  touch-action: none;
}

.range-slider__target {
  position: relative;
}

.range-slider__base,
.range-slider__connects {
  position: relative;
  z-index: 1;

  width: 100%;
  height: 100%;
}

.range-slider__connects {
  z-index: 0;

  height: 4px;
  overflow: hidden;

  background: $color-white-smoke-2;
  border-radius: 4px;
}

.range-slider__connect,
.range-slider__origin {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  transform-origin: 0 0;
  transform-style: flat;

  will-change: transform;
}

.range-slider__connect {
  width: 100%;
  height: 100%;

  background-color: $color-apple;
}

.range-slider__origin {
  width: 10%;
  height: 10%;
}

.range-slider__txt-dir-rtl.range-slider__horizontal .range-slider__origin {
  right: auto;
  left: 0;
}

.range-slider__vertical .range-slider__origin {
  width: 0;
}

.range-slider__horizontal .range-slider__origin {
  height: 0;
}

.range-slider__handle {
  position: absolute;

  width: 24px;
  height: 24px;

  background-color: $color-default-white;
  border: 4px solid $color-apple;
  border-radius: 16px;
  backface-visibility: hidden;
}

.range-slider__touch-area {
  width: 100%;
  height: 100%;
}

.range-slider__state-tap .range-slider__connect,
.range-slider__state-tap .range-slider__origin {
  transition: transform 0.3s;
}

.range-slider__state-drag * {
  cursor: inherit;
}

.range-slider__horizontal {
  height: 14px;
}

.range-slider__horizontal .range-slider__handle {
  top: -10px;
  right: -14px;

  width: 24px;
  height: 24px;
}

.range-slider__txt-dir-rtl.range-slider__horizontal .range-slider__handle {
  right: auto;
  left: -17px;
}

.range-slider__draggable {
  cursor: ew-resize;
}

.range-slider__vertical .range-slider__draggable {
  cursor: ns-resize;
}

[disabled].range-slider__target,
[disabled].range-slider__handle,
[disabled] .range-slider__handle {
  cursor: not-allowed;
}

.range-slider__pips,
.range-slider__pips * {
  box-sizing: border-box;
}

.range-slider__value {
  position: absolute;

  text-align: center;
  white-space: nowrap;
}

.range-slider__pips-horizontal {
  top: 100%;
  left: 0;

  width: 100%;
  height: 80px;
  padding: 10px 0;
}

.range-slider__value-horizontal {
  transform: translate(-50%, 50%);
}

.range-slider__rtl .range-slider__value-horizontal {
  transform: translate(50%, 50%);
}

.range-slider__marker-horizontal.range-slider__marker {
  width: 2px;
  height: 5px;
  margin-left: -1px;
}

.range-slider__marker-horizontal.range-slider__marker-sub {
  height: 10px;
}

.range-slider__marker-horizontal.range-slider__marker-large {
  height: 15px;
}

.range-slider__horizontal .range-slider__tooltip {
  bottom: 120%;
  left: 50%;

  transform: translate(-50%, 0);
}
