.article-inner__sidebar {
  display: block;

  @include vp--1023 {
    display: none;
  }
}

.article-inner__bottom {
  display: flex;
  align-items: flex-start;
}

.article-inner__article {
  max-width: 955px;
  padding-bottom: 120px;

  @include vp--767 {
    padding-bottom: 32px;
  }
}

.article-inner__img {
  margin-bottom: 24px;
  width: 302px;

  & img {
    width: 100%;
    background-color: rgba(11, 11, 11, 0.16);
    object-fit: cover;
  }
}

.article-inner__content {
  position: relative;
  padding-bottom: 24px;
  margin-bottom: 24px;

  & time {
    color: $color-dark-gray;
    margin-bottom: 8px;
    display: block;
  }

  & p {
    margin: 0;
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &::after {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    background-color: $color-white-smoke;
  }
}

.article-inner__link {
  color: $color-apple;

  &:hover {
    opacity: 0.8;
    transition: opacity $trans--300;
  }
}

.article-inner__buttons-container {
  display: flex;
  justify-content: space-between;
}

/* stylelint-disable declaration-no-important */

.article-inner__share {
  display: flex;
  align-items: center;

  & span {
    background-size: 22px 22px !important;
  }

  & .ya-share2__title {
    margin-left: 8px !important;
  }
  /* stylelint-enable declaration-no-important */
}
