.rating {
  display: flex;
  align-items: center;
  margin-right: 17px;
}

.rating--short {
  .rating__icon {
    width: 15px;
    background-image: url("../../img/svg/icon-star-orange.svg");

    &::before {
      display: none;
    }
  }
}

.rating__icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 90px;
  height: 13px;
  margin-right: 6px;

  background-image: url("../../img/svg/icon-star.svg");
  background-size: 18px 13px;

  &::before {
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    left: 0;
    top: 0;

    background-image: url("../../img/svg/icon-star-orange.svg");
    background-size: 18px 13px;
  }

  &--5::before {
    width: 10%;
  }

  &--10::before {
    width: 20%;
  }

  &--15::before {
    width: 30%;
  }

  &--20::before {
    width: 40%;
  }

  &--25::before {
    width: 50%;
  }

  &--30::before {
    width: 60%;
  }

  &--35::before {
    width: 70%;
  }

  &--40::before {
    width: 80%;
  }

  &--45::before {
    width: 90%;
  }

  &--50::before {
    width: 100%;
  }
}

.rating__counter {
  font-size: 14px;
  line-height: 16px;
  color: $color-nero;
}
