.header-middle-basket {
  position: relative;

  display: flex;
  flex-grow: 1;
}

.header-middle-basket__wrap {
  display: flex;
  flex-direction: column;
  margin-right: auto;
  margin-left: 16px;
}

.header-middle-basket__link {
  flex-grow: 1;
}

.header-middle-basket__text {
  margin-top: 0;
  margin-bottom: 0;

  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.header-middle-basket .price-box {
  font-size: 16px;
  line-height: 20px;
}

.header-middle-basket .popup-basket {
  position: absolute;
  top: 57px;
  right: 0;
  z-index: 10;
}
