.popup-basket-table {
  height: 283px;
  display: flex;
  flex-grow: 1;

  border-collapse: separate;
  border-spacing: 0;

  &--padding-top {
    padding-top: 41px;
  }

  &--overflow-x-hidden tbody {
    overflow-y: scroll;
  }
}

.popup-basket-table tbody {
  flex-grow: 1;
  flex-direction: column;
}

.popup-basket-table__row {
  display: grid;
  padding: 16px 32px 16px 16px;

  border-bottom: 1px solid $color-whisper;

  grid-auto-flow: column;
  grid-column: 1 / -1;
  grid-column-gap: 24px;
  grid-template-columns: 419px 112px 51px 59px;

  &--theme-gray {
    color: $color-dark-gray;

    background-color: $color-white-smoke;
  }

  &--size-small {
    padding: 8px 16px;
  }

  &--top-stycky {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10;
  }
}

.popup-basket-table .button {
  justify-content: center;
  align-self: center;
}

.popup-basket-table__ceil {
  display: flex;
  align-items: center;
  padding: 0;

  &--size-small {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
  }

  &--content-horizontal-center {
    justify-content: center;
  }

  &--content-horizontal-right {
    justify-content: flex-end;
  }
}

.popup-basket-table__image {
  flex-shrink: 0;
  width: 64px;
  height: 48px;
  margin-right: 8px;
}

.popup-basket-table__image img {
  height: 100%;

  object-fit: cover;
}
