.checkbox label {
  @include non-touch {
    &:hover::before {
      border-color: $color-very-light-grey;
    }
  }

  position: relative;

  display: block;
  padding-left: 30.4px;

  font-size: 14px;

  cursor: pointer;

  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 0;

    width: 20px;
    height: 20px;

    background-color: $color-default-white;
    border: 1px solid $color-whisper;
    border-radius: 4px;
  }
}

.checkbox svg {
  position: absolute;
  top: 7px;
  left: 3px;

  color: $color-default-white;
}

.checkbox input:active + label,
.checkbox input:focus + label {
  &::before {
    border-color: $color-very-light-grey;
  }
}

.checkbox input:checked + label {
  &::before {
    background-color: $color-apple;
    border: none;
  }

  & svg {
    display: block;
  }
}
