.discount {
  padding: 5px 24px;
  padding-bottom: 24px;
  margin-top: 24px;
  margin-bottom: 24px;

  background-color: $color-rose-white;

  border-radius: 8px;

  h2 {
    margin-bottom: 11px;

    font-weight: 500;
    font-size: 24px;
    line-height: 32px;

    a {
      color: $color-safety-orange;
      border-bottom: 1px solid $color-safety-orange;
    }
  }

  p {
    font-size: 16px;
    line-height: 24px;
    margin: 0;

    span {
      display: block;
      padding: 8px 0;
      margin-top: 11px;

      text-align: center;
      font-weight: 500;
      font-size: 24px;
      line-height: 24px;
      color: $color-safety-orange;

      border: 1px solid $color-tacao;
      border-radius: 8px;
    }
  }
}
