@include non-touch {
  .brands-catalog__more-brand-link:hover {
    color: $color-nero;

    transition: $trans--300;
    transition-property: color;
  }
}

.brands-catalog {
  @include vp--1023 {
    display: none;
  }
}

.brands-catalog__alphabet-list {
  margin-right: 25px;
  flex-grow: 1;
  display: flex;
}

.brands-catalog__button-list {
  display: flex;
  margin-right: 16px;
}

.brands-catalog__wrap-top {
  display: flex;
  align-items: center;
  padding: 20px 16px;

  border-bottom: 1px solid $color-whisper;
}

.brands-catalog .tabs-flags {
  margin-right: 24px;
}

.brands-catalog__more-brand-link {
  color: $color-apple;
  margin-left: auto;
}

.brands-catalog__more-brand-link .link__icon {
  margin-left: 11px;
}

.brands-catalog__wrap-bottom {
  padding: 24px;
}

.brands-catalog__title {
  margin-top: 0;
  margin-bottom: 24px;

  color: $color-dark-gray;
}

.brands-catalog__list-links {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  list-style: none;
  column-count: 5;
  column-gap: 24px;
}

.brands-catalog__item-list-links {
  margin-bottom: 24px;

  font-weight: 400;

  break-inside: avoid;
}

.brands-catalog__link {
  font-weight: 400;
}

.brands-catalog__item-list-links:nth-child(4n) {
  margin-bottom: 0;
}

.brands-catalog__content {
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  box-shadow: 0 0 10px 0 rgba(204, 204, 204, 35%);
}

.brands-catalog .brands-catalog__alphabet {
  position: static;

  &--active {
    display: flex;
  }
}

.brands-catalog .alphabet {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.brands-catalog .is-active {
  position: static;

  width: auto;
  height: auto;
  margin: 0;

  clip: auto;
}
