.categories {
  padding: 0;

  list-style: none;
}

.categories__chapter {
  margin-bottom: 1px;

  &:first-child {
    border-radius: 8px 8px 0 0;
  }

  &:last-child {
    margin-bottom: 0;

    border-radius: 0 0 8px 8px;
  }
}

.categories__chapter--small {
  margin-bottom: 16px;
}

.categories__section {
  padding-left: 16px;

  list-style: none;
}

.categories [data-accordion="element"].is-open .chapter__icon {
  transform: rotate(0);
}


.categories [data-accordion="element"].is-open > [data-accordion="content"] {
  max-height: 100%;
  margin-top: 16px;
}

.categories[data-accordion="parent"].no-transition [data-accordion="content"] {
  transition: none;
}

.categories [data-accordion="content"] {
  max-height: 0;
  overflow: hidden;

  transition: max-height 0.3s;
}
