.categories-tags {
  @media (min-width: 769px) {
    display: flex;
    flex-direction: row;
  }
}

.swiper-container.categories-tags {
  overflow: visible;
  min-width: 0;
  transition-duration: 0ms;
  transform: translate3d(0, 0, 0);
}

.categories-tags__wrapper {
  @media (min-width: 769px) {
    margin-left: -16px;
    margin-top: -16px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.swiper-wrapper.categories-tags__wrapper {
  min-width: 0;
}

.swiper-slide.categories-tags__slide {
  width: auto;
  margin-right: 16px;
}

.categories-tags__slide {
  @media (min-width: 769px) {
    margin-left: 16px;
    margin-top: 16px;
  }
}
