.rating-select {
  display: flex;
  align-items: center;
  margin-bottom: 16px;

  color: $color-dark-gray;
}

.rating-select__field {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin: 0 10px;

  label {
    display: flex;
    align-items: center;
    padding: 0 3px;

    cursor: pointer;
  }

  svg {
    color: $color-dark-gray;
  }

  input:checked ~ label svg {
    color: $color-safety-orange;
  }

  label:hover svg,
  label:hover ~ label svg {
    /* stylelint-disable-next-line */
    color: $color-safety-orange !important;
  }
}

.rating-select__field:hover {
  svg {
    /* stylelint-disable-next-line */
    color: $color-dark-gray !important;
  }
}
