.home-catalog__wrapper {
  @include vp--767 {
    display: none;
  }
}

.home-catalog__controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid $color-whisper;
}

.home-catalog__controls-wrapper {
  max-width: 455px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.home-catalog__content-zoo,
.home-catalog__content-children {
  display: none;

  &--active {
    width: 100%;
    margin-bottom: 24px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 48px 56px;
  }
}

.home-catalog__sliders {
  display: none;

  @include vp--767 {
    display: block;
  }
}
