.category-page__breadcrumbs {
  margin-top: 23px;
  margin-bottom: 0;
}

.category-page__main {
  display: flex;
  margin-bottom: 64px;
}

.category-page__sidebar {
  flex-shrink: 0;
  min-width: 240px;
  max-width: 302px;
  margin-right: 24px;
}

.category-page__categories {
  margin: 0;
  margin-bottom: 24px;
}

.category-page__filter {
  margin: 0;
}

.category-page__products {
  flex-grow: 1;
  padding-top: 8px;
}

.category-page__products-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 24px;
  margin-left: -24px;
  padding: 0;

  list-style: none;
}

.category-page__products-item {
  margin-top: 24px;
  margin-left: 24px;

  &--next-dummy {
    display: flex;
    padding-bottom: 24px;
    order: 999;
  }
}

.category-page__sort {
  @include vp--767 {
    display: none;
  }

  margin-bottom: 15px;
}

.category-page__bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  margin-bottom: 54px;
}

.category-page__pagination {
  margin: 0;
}
