@include non-touch {
  .drop-down-seach__link:hover {
    color: $color-apple;

    background-color: $color-panache;
  }
}

.drop-down-seach {
  padding: 8px;

  background-color: $color-default-white;
  border-radius: 8px;
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.1);
}

.drop-down-seach__item {
  display: flex;
  flex-direction: column;
}

.drop-down-seach__link {
  display: inline-block;
  flex-grow: 1;
  padding: 8px;

  border-radius: 4px;
  overflow: hidden;

  &--flex {
    display: flex;
  }
}

.drop-down-seach__list {
  margin-top: 0;
  margin-bottom: 8px;
  padding-left: 0;

  list-style: none;
}

.drop-down-seach__list:last-of-type {
  margin-bottom: 0;
}

.drop-down-seach__list + .drop-down-seach__list {
  padding-top: 8px;

  border-top: 1px solid $color-whisper;
}

.drop-down-seach__text {
  font-weight: 400;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.drop-down-seach__title {
  margin-top: 0;
  margin-bottom: 0;
  padding: 8px;

  font-size: 14px;
  line-height: 20px;
  color: $color-dark-gray;
}

.drop-down-seach__message {
  padding: 8px;

  font-weight: 400;
  color: $color-dark-gray;
}

.drop-down-seach__image {
  flex-shrink: 0;
  width: 72px;
  height: 48px;
  margin-right: 12px;

  background-color: $color-white;
  border-radius: 4px;
}

.drop-down-seach__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
