.breadcrumbs {
  @include vp--767 {
    margin-right: -16px;
    margin-left: -16px;
    padding: 0 16px;
    flex-wrap: nowrap;
  }

  display: flex;
  flex-wrap: wrap;
  padding: 0;
  overflow: auto;

  list-style: none;

  user-select: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.breadcrumbs__item {
  white-space: nowrap;

  &:not(:last-child)::after {
    content: "";

    display: inline-block;
    width: 4px;
    height: 4px;
    margin-right: 10px;
    margin-left: 6px;

    vertical-align: middle;

    background-color: $color-whisper;
    border-radius: 4px;
  }
}

.breadcrumbs__link {
  font-size: 14px;
  line-height: 24px;
  color: $color-dark-gray;
}
