.radio label {
  position: relative;

  display: block;

  color: $color-charcoal;

  border-bottom: 1px dashed $color-very-light-grey;
  cursor: pointer;
}

.radio--radiobutton label {
  @include non-touch {
    &:hover::before {
      border-color: $color-very-light-grey;
    }
  }

  padding-left: 32px;

  border-bottom: none;

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;

    width: 20px;
    height: 20px;

    background-color: $color-default-white;
    border: 1px solid $color-whisper;
    border-radius: 20px;
  }
}


.radio input:checked + label {
  border-bottom-color: $color-madang;
}

.radio--radiobutton {
  & input:checked + label {
    &::before {
      background-color: $color-apple;
      border: none;
    }

    &::after {
      content: "";
      position: absolute;
      top: 8px;
      left: 8px;

      width: 8px;
      height: 8px;

      background-color: $color-default-white;
      border-radius: 20px;
    }
  }

  & input:active + label,
  & input:focus + label {
    &::before {
      border-color: $color-very-light-grey;
    }
  }
}

.radio--font-bold label {
  font-weight: 500;
}

.radio--vertical-paddings {
  & label {
    padding-top: 16px;
    padding-bottom: 15px;
  }

  & label::before {
    top: 18px;
  }

  & input:checked + label::after {
    top: 24px;
  }
}

.radio--active-text-green input:checked + label {
  color: $color-apple;
}
