.error {
  max-width: 628px;
  margin: 146px auto;
  text-align: center;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.error__name {
  font-weight: 900;
  font-size: 96px;
  line-height: 96px;
  color: $color-apple;
}

.error__title {
  font-size: 24px;
  line-height: 28px;
  font-weight: normal;

  margin: 8px 0;

  @include vp--767 {
    font-size: 18px;
    line-height: 24px;
  }
}

.error__text {
  margin: 0;

  @include vp--767 {
    font-size: 14px;
    line-height: 20px;
  }
}

.error__link {
  color: $color-apple;

  @include non-touch {
    &:hover {
      color: $color-tacao;
    }
  }
}
