.reviews {
  max-width: 954px;

  @include vp--767 {
    width: 100%;
  }
}

.reviews__item {
  border: 1px solid $color-whisper;
  border-radius: 8px;

  margin-bottom: 24px;
}

.reviews__top {
  padding: 24px;
  border-bottom: 1px solid $color-whisper;
  width: 100%;
  display: flex;
  align-items: center;

  @include vp--767 {
    flex-wrap: wrap;
    padding: 16px;
  }
}

.reviews__name {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin-right: 24px;
  width: 81%;

  & p {
    margin: 0;
  }

  & .reviews__img {
    align-self: flex-start;
  }

  @include vp--767 {
    width: 100%;
    margin: 0;
    margin-bottom: 8px;
  }
}

.reviews__amount {
  margin-right: 23px;
  min-width: 34px;

  text-align: right;
  color: $color-dark-gray;

  @include vp--767 {
    margin-right: 16px;
    margin-left: 64px;
  }
}

.reviews__price {
  width: 96px;
  text-align: right;
  font-weight: 500;

  @include vp--767 {
    width: auto;
  }
}

.reviews__review {
  padding: 24px;

  display: flex;
}

.reviews .reviews__user-question {
  padding: 24px;
}

.reviews__inner {
  width: 100%;
}

.reviews__img {
  min-width: 48px;
  margin-right: 16px;
}

.reviews__header {
  margin-bottom: 20px;
  position: relative;

  .rating {
    margin-top: 5px;
  }

  .rating span {
    font-size: 0;
  }

  .comment-rating {
    position: absolute;
    right: 0;
    top: 0;
  }

  .comment-rating__like {
    margin-right: 14px;
  }

  .comment-rating__dislike {
    margin-right: 0;
  }
}

.reviews__name-autor {
  margin-right: 16px;

  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.reviews__date {
  color: $color-very-light-grey;
}


.reviews__content {
  color: $color-charcoal;

  p {
    margin-bottom: 20px;
  }
}

.reviews__images {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 20px;
  margin-right: -16px;
  margin-bottom: -20px;

  img {
    margin-right: 16px;
    margin-bottom: 20px;
  }
}

.reviews__footer {
  display: flex;
  align-items: center;

  button {
    margin-right: 24px;
    font-weight: normal;
  }
}

.reviews__remove {
  svg {
    margin-right: 10px;
  }
}

.reviews .reviews__show-more {
  text-align: center;
  width: 100%;
  display: block;
}

.reviews__modal .modal__title {
  font-size: 18px;
  margin-right: 20px;
}

.reviews__modal .modal__content {
  width: 410px;

  @include vp--767 {
    width: 100%;
  }
}

.reviews__modal-content {
  display: flex;
}

.reviews__modal .reviews__button {
  display: block;
  width: 47.79%;
  margin-right: 16px;
  text-align: center;
}


.reviews__modal .reviews__button--no-margin {
  margin: 0;
}
