.product-questions {
  padding: 0 18px;

  textarea {
    width: 100%;
    height: 96px;
    padding: 10px 14px;
    margin-bottom: 14px;

    color: $color-dark-gray;
    font-size: 14px;

    border-color: $color-whisper;
    border-radius: 4px;
  }

  .writing-button {
    margin-bottom: 20px;
  }
}

.product-questions__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.product-questions__content {
  margin-bottom: 20px;
}
