.share {
  position: relative;
  display: inline-block;
  vertical-align: middle;

  .ya-share2__title {
    padding-right: 5px;
    font-size: 14px;
    line-height: 16px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 200%;
    top: 0;
    left: 0;
  }

  /* stylelint-disable declaration-no-important */

  .ya-share2__item_more.ya-share2__item_has-pretty-view .ya-share2__icon_more {
    background-image: url("../img/svg/Union.svg") !important;
  }

  .ya-share2__container_size_m .ya-share2__item_more.ya-share2__item_has-pretty-view .ya-share2__link_more {
    padding: 0;
  }

  .ya-share2__list.ya-share2__list_direction_horizontal > .ya-share2__item {
    margin: 0 4px 0 0;
  }

  .ya-share2__container_size_m .ya-share2__popup:not(.ya-share2__popup_mobile) {
    border-radius: 0;
    border-top: 1px solid $color-safety-orange;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: -9px;
      width: 0;
      height: 0;

      border-width: 4px 3px;
      border-style: solid;
      border-color: transparent transparent $color-safety-orange transparent;
    }
  }

  &:hover {
    .ya-share2__popup {
      display: block;
    }
  }

  .ya-share2__container_size_m .ya-share2__item_more.ya-share2__item_has-pretty-view .ya-share2__popup_direction_bottom {
    top: 26px;
    right: 50%;
    transform: translateX(50%);
  }

  .ya-share2__popup {
    width: 143px;
    padding: 20px 25px;

    .ya-share2__title {
      font-size: 0;
    }

    .ya-share2__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: calc(28px * 3 + 4px * 2); // 28px - ширина иконки, 4px - расстояние между иконками
      margin-bottom: -7px;
      padding: 0 !important;
    }

    .ya-share2__badge .ya-share2__icon {
      width: 28px;
      height: 28px;
      background-size: 28px 28px;
    }

    .ya-share2__item {
      width: 28px;
      margin-bottom: 7px !important;
      padding: 0 !important;
    }

    .ya-share2__item_copy {
      display: none !important;
    }
  }

  & a {
    background-color: $color-transparent !important;
    border: none !important;

    &:hover {
      opacity: 0.8 !important;
    }

    &:focus {
      opacity: 0.5 !important;
    }
  }

  /* stylelint-enable declaration-no-important */
  & span {
    color: $color-safety-orange;
    font-family: $font-family--main;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }
}
